import Image from '@repo/ui/components/common/Image'
import { Icon } from '@repo/ui/icon'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { getWinNumberStore } from '@repo/ui/store/useWinNumberStore'
import { RACE_ORDER } from '@repo/ui/utils/roulette'

import polarCenter from '@roulette/assets/statistic/polarCenter.webp'
import radial from '@roulette/assets/statistic/radial.webp'

import { cn } from '@repo/utils/common/common'
import { getBetColor } from '@roulette/service/bet'
import { useStatisticStore } from '@roulette/stores/useStatisticStore'
import PolarChart from './PolarChart'
import './Statistics.scss'

const useWinNumberStore = getWinNumberStore<string>()

const calcTotal = (history: string[], count: number) => {
  const mapping = new Map(RACE_ORDER.map((num) => [num, 0]))

  history.slice(-count).forEach((str) => {
    const num = Number(str)
    mapping.set(num, mapping.get(num)! + 1)
  })

  return Array.from(mapping.entries())
}

const StatisticHotCold = () => {
  const history = useWinNumberStore((state) => state.history)
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)

  const numberOfRounds = useStatisticStore((state) => state.numberOfRounds)
  const mapping = calcTotal(history, numberOfRounds)

  const sorted = [...mapping].sort((a, b) => b[1] - a[1]).map((arr) => arr[0])
  const hotArr = sorted.slice(0, 5)
  const coldArr = sorted.slice(-5).reverse()

  return (
    <div
      id="StatisticHotCold"
      className="center mr-[0.2rem] flex h-full gap-4 pb-[1.9rem] pt-8"
    >
      <div className="center resultCol flex-col gap-[0.6rem]">
        <div className="center relative h-[1.75rem] w-6">
          <Icon.Hot className="w-5" />
        </div>
        {hotArr.map((bet, idx) => (
          <div
            key={idx}
            className={cn('bet', getBetColor(bet))}
          >
            {bet}
          </div>
        ))}
      </div>

      <div className="relative h-[13.4rem] w-[13.6rem]">
        <Image
          src={radial}
          className={cn('mt-[-6%] w-[106%] max-w-none', isSmallDevice && 'mt-[6%]')}
          alt="Radial Background"
        />
        {history.length > 0 && (
          <PolarChart
            mapping={mapping}
            hotArr={hotArr}
            coldArr={coldArr}
          />
        )}
        <Image
          src={polarCenter}
          className={cn('absolute left-[41.3%] top-[41.3%] w-[2.3rem]', isSmallDevice && 'left-[39%]')}
          alt="Polar Center"
        />
      </div>

      <div className="center resultCol typeCold flex-col gap-[0.6rem]">
        <div className="center relative h-[1.75rem] w-6">
          <Icon.Cold className="w-5" />
        </div>
        {coldArr.map((bet, idx) => (
          <div
            key={idx}
            className={cn('bet', getBetColor(bet))}
          >
            {bet}
          </div>
        ))}
      </div>
    </div>
  )
}

export default StatisticHotCold
