import toggle from '@repo/ui/assets/setting/toggle.webp'
import Image from '@repo/ui/components/common/Image'
import { cn } from '@repo/utils/common/common'
import { playSound } from '@repo/utils/common/sound'
import usePreventPullToRefresh from '@repo/utils/hook/usePreventPullToRefresh'
import { memo, useRef } from 'react'
import './SliderBar.scss'

const BaseBar = ({ className }: { className?: string }) => (
  <div className={cn('h-[1.125rem] w-[0.1875rem] rounded-[4.375rem] bg-[#A57D4E]', className)}></div>
)
const sliderIndex = (() => {
  let index = 1
  return {
    increment() {
      index = index === 8 ? 1 : index + 1
    },
    value() {
      return index
    },
  }
})()

type BackgroundRangeProps = {
  percent: number
  intervalCount?: number
}

function BackgroundRange({ percent, intervalCount = 40 }: BackgroundRangeProps) {
  return (
    <div className="center w-full gap-[0.1875rem]">
      <BaseBar key={-2} />
      <BaseBar key={-1} />
      {Array.from({ length: intervalCount + 1 }, (_, i) => i).map((_, idx) => {
        if ((idx + 5) / intervalCount < percent) return <BaseBar key={idx} />
        if ((idx + 4) / intervalCount < percent)
          return (
            // Tall bar
            <BaseBar
              className="h-[1rem]"
              key={idx}
            />
          )
        if ((idx + 3) / intervalCount < percent)
          return (
            // slightly taller bar
            <BaseBar
              className="h-[0.875rem]"
              key={idx}
            />
          )
        return (
          // short bar
          <BaseBar
            className="h-[0.75rem] bg-[#414141]"
            key={idx}
          />
        )
      })}
    </div>
  )
}

export type SliderBarProps = {
  value: number
  min?: number
  max?: number
  step?: number
  intervalCount?: number
  onChange: (e: number) => void
}
function SliderBar(props: SliderBarProps) {
  const { value, min = 0, max = 1, step = 0.01, intervalCount, onChange } = props
  const percentage = (value - min) / (max - min)

  const sliderBarRef = useRef<HTMLDivElement>(null)
  usePreventPullToRefresh(sliderBarRef)

  return (
    <div
      id="SliderBar"
      ref={sliderBarRef}
      className="relative w-full"
    >
      <BackgroundRange
        percent={Number(percentage)}
        intervalCount={intervalCount}
      />
      <div className="ballRange">
        <div
          className="absolute h-full"
          style={{ left: Math.max(0, percentage * 100) - 0.5 + '%' }}
        >
          <Image
            src={toggle}
            className="absolute left-1/2 top-1/2 w-7 max-w-none -translate-x-1/2 -translate-y-1/2 transform"
            alt="Slider control"
          />
        </div>
      </div>
      <input
        className="absolute left-0 top-0"
        type="range"
        min={min}
        max={max}
        step={step}
        value={value}
        onChange={(e) => {
          playSound([`slider${sliderIndex.value()}`])
          sliderIndex.increment()
          onChange(+e.target.value)
        }}
      />
    </div>
  )
}

export default memo(SliderBar)
