import { useAnimate } from 'framer-motion'
import { useEffect } from 'react'

import { getWinNumberStore } from '@repo/ui/store/useWinNumberStore'
import { cn, isDesktop } from '@repo/utils/common/common'

import { getBetColor } from '@roulette/service/bet'
import './BetResult.scss'

const useWinNumberStore = getWinNumberStore<string>()

const BetResult = ({ className }: { className?: string }) => {
  const history = useWinNumberStore((state) => state.history)
  const historyDisplay = history.slice(-16).reverse()
  const [scope, animate] = useAnimate()

  useEffect(() => {
    animate(scope.current, { x: ['-1.5rem', 0] }, { duration: 0.4, ease: 'linear' })
  }, [animate, history, scope])

  return (
    <div
      className={cn({ isDesktop }, className)}
      id="BetResult"
    >
      <div
        ref={scope}
        className="resultList"
      >
        {historyDisplay.map((num, i) => (
          <div
            key={`${num}-${i}`}
            className={`${getBetColor(num)}`}
          >
            {num}
          </div>
        ))}
      </div>
    </div>
  )
}

export default BetResult
