import { useState } from 'react'

function ActiveStateWrapper({ children, duration = 200 }: { children: React.ReactElement; duration?: number }) {
  const [clicked, setClicked] = useState<boolean>(false)
  const handleClick = () => {
    setClicked(true)
    let timer = 0
    clearTimeout(timer)
    timer = window.setTimeout(() => {
      setClicked(false)
    }, duration)
  }
  return (
    <div
      onClick={handleClick}
      className={`${clicked ? 'active' : ''}`}
    >
      {children}
    </div>
  )
}

export default ActiveStateWrapper
