import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { getWinNumberStore } from '@repo/ui/store/useWinNumberStore'

import { cn, isMobileDevice } from '@repo/utils/common/common'
import { getBetColor } from '@roulette/service/bet'
import { useStatisticStore } from '@roulette/stores/useStatisticStore'
import './Statistics.scss'

const useWinNumberStore = getWinNumberStore<string>()

const StatisticLast500 = () => {
  const history = useWinNumberStore((state) => state.history)

  const numberOfRounds = useStatisticStore((state) => state.numberOfRounds)
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)

  // Calculate how many items should be in each row
  const itemsPerRow = isSmallDevice ? 6 : 8
  // Each item is 1.875rem wide with a 0.75rem gap (gap-3)
  const containerWidth = `calc(${itemsPerRow} * 1.875rem + ${itemsPerRow - 1} * 0.75rem)`
  return (
    <div
      id="StatisticLast500"
      className={cn('center pb-[11.25rem] pt-2', {
        'pt-3': isMobileDevice,
      })}
    >
      <div
        className="flex max-w-full flex-wrap gap-3"
        style={{ width: containerWidth }}
      >
        {[...history]
          .reverse()
          .slice(0, numberOfRounds)
          .map((bet, idx) => (
            <div
              key={idx}
              className={cn('bet', getBetColor(bet))}
            >
              {bet}
            </div>
          ))}
      </div>
    </div>
  )
}

export default StatisticLast500
