import { GetAddChipParamsFn } from '@repo/utils/hook/useBettingBoard'
import { betGridPrefix } from '@roulette/service/bet'
import { ALL_GROUP_MAP } from '@roulette/service/group'
import { BetTypeEnum } from '@roulette/types/betType'
import { ReactNode } from 'react'

// Common types
export interface LatticeProps {
  pos: number[]
  id: string
  className?: string
  children?: ReactNode
  highlights?: string[]
}

// Animation scale values for chip appearance
export const chipScaleAnimation = [
  0.9, 0.9836, 1.0311, 1.0598, 1.0775, 1.0882, 1.0945, 1.0978, 1.0995, 1.1, 1.0979, 1.092, 1.0754, 1.0403, 1.0189, 1.0088, 1.0032, 1.0015,
  1,
]

// Convert a bet type to a group of related bet types
export const betTypeToGroup = (betType: BetTypeEnum): BetTypeEnum[] => {
  if (ALL_GROUP_MAP[betType]) return ALL_GROUP_MAP[betType]
  return [betType]
}

// Generate number array for grid layout
export const generateNumberArray = (length: number, startAt = 0, transform: (i: number) => number = (i) => i) =>
  Array.from({ length }, (_, i) => transform(i + startAt))

// Common grid generators
export const generateMainStraightUpCells = (highlights: string[]) => {
  return generateNumberArray(12, 1)
    .map((num) => [
      { pos: [3, 6, num * 3, num * 3 + 3], id: `straight-${String(num * 3 - 2)}`, highlights },
      { pos: [6, 9, num * 3, num * 3 + 3], id: `straight-${String(num * 3 - 1)}`, highlights },
      { pos: [9, 12, num * 3, num * 3 + 3], id: `straight-${String(num * 3)}`, highlights },
    ])
    .flat()
}

export const generateInvisibleGridCells = (highlights: string[]) => {
  const cornerAndSplitCells = generateNumberArray(11, 5, (i) => i * 3)
    .map((num) => [
      { pos: [5, 7, num, num + 2], id: `corner-${num - 4}_${num - 3}_${num - 1}_${num}`, highlights },
      { pos: [8, 10, num, num + 2], id: `corner-${num - 3}_${num - 2}_${num}_${num + 1}`, highlights },
      { pos: [4, 5, num, num + 2], id: `split-${num - 4}_${num - 1}`, highlights },
      { pos: [7, 8, num, num + 2], id: `split-${num - 3}_${num}`, highlights },
      { pos: [10, 11, num, num + 2], id: `split-${num - 2}_${num + 1}`, highlights },
    ])
    .flat()

  const streetAndSplitCells = generateNumberArray(12, 4, (i) => i * 3)
    .map((num) => [
      { pos: [2, 4, num, num + 1], id: `street-${num - 3}_${num - 2}_${num - 1}`, highlights },
      { pos: [5, 7, num, num + 1], id: `split-${num - 3}_${num - 2}`, highlights },
      { pos: [8, 10, num, num + 1], id: `split-${num - 2}_${num - 1}`, highlights },
    ])
    .flat()

  const lineCells = generateNumberArray(11, 0, (i) => i * 3).map((num) => ({
    pos: [2, 4, num + 5, num + 7],
    id: `line-${num + 1}`,
    highlights,
  }))

  return [...cornerAndSplitCells, ...streetAndSplitCells, ...lineCells]
}

export const getAddChipParams: GetAddChipParamsFn = (event) => {
  const { pageX: x, pageY: y } = event
  // Prevent same event target on mobile upon pointer down and up
  const target = document.elementFromPoint(x, y) as HTMLSpanElement
  const isValid = target?.id?.startsWith(betGridPrefix)
  const betType = isValid ? (target.id.slice(betGridPrefix.length) as BetTypeEnum) : ''
  return betType
}
