import { motion } from 'framer-motion'
import { useEffect, useState } from 'react'

import ChipSelectorChip from '@repo/ui/components/chipTray/Chip'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useWinAnimStore } from '@repo/ui/store/useWinAnimStore'
import { BetState } from '@repo/utils/types/betstate'
import './Chip.scss'

interface NormalChipProps {
  is3D?: boolean
  isWin: boolean
  chipId: string
  chipRef: React.RefObject<HTMLDivElement | null>
  chipSkewXDeg?: number
  value: number
}
const chipVariants = {
  initial: { scale: 1, opacity: 1, filter: 'brightness(1) grayscale(0)' },
  idle: (custom: { shouldSkipIdle: boolean }) =>
    custom.shouldSkipIdle
      ? {
          scale: 1,
        }
      : {
          scale: [
            0.9, 0.9836, 1.0311, 1.0598, 1.0775, 1.0882, 1.0945, 1.0978, 1.0995, 1.1, 1.0979, 1.092, 1.0754, 1.0403, 1.0189, 1.0088, 1.0032,
            1.0015, 1,
          ],
          transition: {
            duration: 0.58,
            ease: 'easeInOut',
          },
        },
  disappear: {
    scale: 0.95,
    opacity: 0,
    filter: 'brightness(0.3) grayscale(1)',
    transition: {
      scale: { duration: 0.6, ease: 'easeOut' },
      opacity: { duration: 0.6, ease: 'easeOut' },
      filter: { duration: 0.333, ease: 'easeOut', delay: 0.1667 },
    },
  },
  increase: {
    scale: [1, 1.14, 1.14, 1, 1],
    opacity: [1, 1, 1, 0, 0],
    transition: {
      duration: 2,
      times: [0, 0.1165, 0.5035, 0.6, 1],
      ease: 'easeIn',
      delay: 0.333,
    },
  },
}

export const NormalChip = ({ isWin, chipId, is3D, value, chipRef, chipSkewXDeg }: NormalChipProps) => {
  const gameState = useBettingStore((s) => s.gameState)
  const animState = useWinAnimStore((s) => s.animState)
  const runWinAnimation = !!animState

  const [skipIdle, setSkipIdle] = useState(false)
  const [chipResume, setChipResume] = useState(true)

  useEffect(() => {
    setSkipIdle(false)
  }, [value])

  useEffect(() => {
    //Check the game state when animation start, if animation start during settlement period, need to wait until settlement end to resume the chips.
    if (animState === 'animationStart' && gameState === BetState.Settlement) {
      setChipResume(false)
    }
  }, [animState, gameState])

  const getAnimation = () => {
    //If chipResume is false, wait until settlement end to resume; otherwise can resume immediately
    const chipResumeNow = (!chipResume && gameState !== BetState.Settlement) || chipResume
    if (!runWinAnimation && animState === null && chipResumeNow) return 'idle'
    return isWin ? 'increase' : 'disappear'
  }

  return (
    <motion.div
      key={chipId + value}
      initial={'initial'}
      animate={getAnimation()}
      variants={chipVariants}
      ref={chipRef}
      className={'pointer-events-none absolute w-full'}
      custom={{ shouldSkipIdle: skipIdle }}
      onAnimationStart={(e) => {
        if (e !== 'idle') {
          setSkipIdle(true)
        } else {
          setChipResume(true)
        }
      }}
    >
      <ChipSelectorChip
        value={value}
        isDisabled={false}
        is3D={is3D}
        chipId={chipId}
        chipSkewXDeg={chipSkewXDeg}
      />
    </motion.div>
  )
}
