import { useTimerStore } from '@repo/ui/store/useTimerStore'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import './Clock.scss'

export default function Clock() {
  const currentBySec = useTimerStore((state) => state.currentSec)
  if (currentBySec === 0) return ''

  return (
    <span
      id="Clock"
      className={cn({ isMobileDevice })}
    >
      {new Date(currentBySec * 1000).toLocaleString('en-US', {
        hour: '2-digit',
        minute: '2-digit',
        hour12: false,
        ...(isMobileDevice && { second: '2-digit' }),
      })}
    </span>
  )
}
