import { useHistoryStore } from '@repo/ui/store/useHistoryStore'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import { memo } from 'react'
import { useTranslation } from 'react-i18next'
import DateGroup from './DateGroup'
import EmptyHistory from './EmptyHistory'
import './GameHistory.scss'

function GameHistory() {
  const groupedHistoryId = useHistoryStore((state) => state.groupedHistoryId)
  const historyKeys = Object.keys(groupedHistoryId)
  const { t } = useTranslation()

  return historyKeys.length === 0 ? (
    <EmptyHistory />
  ) : (
    <div
      id="GameHistory"
      className={cn('mt-1.5', {
        'mt-2.5': isMobileDevice,
      })}
    >
      <div className="gridHeader">
        <div className="gridRow uppercase">
          <div className="gridCell">{t('COMMON.DATE')}</div>
          <div className="gridCell">{t('COMMON.GAME')}</div>
          <div className="gridCell">{t('COMMON.TOTAL_BET')}</div>
          <div className="gridCell">{t('COMMON.RESULT')}</div>
        </div>
      </div>
      <div>
        {historyKeys.map((date) => (
          <DateGroup
            key={date}
            date={date}
          />
        ))}
      </div>
    </div>
  )
}

export default memo(GameHistory)
