import { AnimatePresence, motion } from 'framer-motion'
import { ReactNode } from 'react'

type AnimationDirection = 'up' | 'down' | null
interface AnimateCounterProps {
  value: number | string
  className?: string
  direction?: AnimationDirection
  reverseDirection?: boolean // If true, reverses the direction of the animation
  axis?: 'x' | 'y'
  children?: ReactNode
  duration?: number
  distance?: number
}

export default function AnimateCounter({
  value,
  className = '',
  direction = null,
  reverseDirection = false,
  axis = 'y',
  duration = 0.25,
  distance = 28,
}: AnimateCounterProps) {
  const variants = {
    enter: (direction: AnimationDirection) => {
      const actualDirection = reverseDirection && direction ? (direction === 'up' ? 'down' : 'up') : direction

      return {
        [axis]: actualDirection === 'up' ? -distance : distance,
        opacity: 0,
      }
    },
    center: {
      [axis]: 0,
      opacity: 1,
    },
    exit: (direction: AnimationDirection) => {
      const actualDirection = reverseDirection && direction ? (direction === 'up' ? 'down' : 'up') : direction

      return {
        [axis]: actualDirection === 'up' ? distance : -distance,
        opacity: 0,
      }
    },
  }

  return (
    <AnimatePresence
      mode="sync"
      custom={direction}
    >
      <motion.div
        key={value}
        custom={direction}
        variants={variants}
        initial="enter"
        animate="center"
        exit="exit"
        transition={{
          duration,
          ease: 'easeOut',
        }}
        className={className}
      >
        {value}
      </motion.div>
    </AnimatePresence>
  )
}
