import { MessageContentGameHistory, MessageHistoryPayload } from '@ikigaians/house'
import { cn, getFormattedRoundId } from '@repo/utils/common/common'
import { useTranslation } from 'react-i18next'

import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { getSicBoNumberColor } from '@repo/ui/utils/types/betType.sicbo'

import { WinNumber } from './SicboWinNumberResult'

const BubbleGameResult = (props: { data: MessageHistoryPayload }) => {
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)
  const current = props.data.content as MessageContentGameHistory
  const result = current.result?.['sicBo'] ?? []

  const color = !current.result || result.length === 0 ? 'cancel' : getSicBoNumberColor(result)
  const { t } = useTranslation()

  return (
    <div className={cn('g-winNumberFrame overflow-visible', color, 'my-3 h-[8.5rem]')}>
      {result.length === 3 && (
        <>
          <div className={cn('absolute top-4 w-[12rem]', isSmallDevice ? 'right-[-2rem]' : 'right-[-1rem]')}>
            <h3 className="text-center">{t('COMMON.GAME_RESULT')}:</h3>
          </div>
          <div
            className={cn(
              'center absolute bottom-[-0.2rem] flex h-[5rem] w-[5rem] scale-[0.95]',
              isSmallDevice ? 'right-[1.5rem]' : 'right-[2.5rem]'
            )}
          >
            <WinNumber win={result} />
          </div>
        </>
      )}
      <div className="absolute left-4 top-4">
        <h2>{t('COMMON.LIVE_SICBO')}</h2>
        <div className="mt-2">
          <div className="grid grid-cols-2 gap-x-10">
            <div>
              <h3>{t('COMMON.GAME_RESOLVED')}:</h3>
              <span>
                {current.finishedAt &&
                  new Date(current.finishedAt).toLocaleString('en-US', {
                    year: 'numeric',
                    month: '2-digit',
                    day: '2-digit',
                    hour: '2-digit',
                    minute: '2-digit',
                    second: '2-digit',
                    hour12: true,
                    timeZone: 'UTC',
                  })}
              </span>
            </div>
            {(result.length < 3 || !result) && (
              <div>
                <h3>{t('COMMON.GAME_RESULT')}:</h3>
                <span>{t('COMMON.GAME_CANCELED')}</span>
              </div>
            )}
          </div>
          <div className="mt-3">
            <h3>{t('COMMON.GAME_NUMBER')}:</h3>
            <span>{`#${getFormattedRoundId(current?.roundId ?? '')}`}</span>
          </div>
        </div>
      </div>
    </div>
  )
}

export default BubbleGameResult
