import { memo, useEffect, useRef, useState } from 'react'

import { Icon } from '@repo/ui/icon'
import { useHistoryStore } from '@repo/ui/store/useHistoryStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { currencyFormat } from '@repo/utils/common/currency'
import { playSound } from '@repo/utils/common/sound'

import GameHistoryResult from './GameHistoryResult'
import GroupItem from './GroupItem'

type DateGroupProps = { date: string }

function DateGroup(props: DateGroupProps) {
  const { date } = props
  const { histories, groupedHistoryId, daySubtotals } = useHistoryStore((state) => state)
  const rootEmFontSize = useLayoutStore((s) => s.rootEmFontSize)
  const { bet, win } = daySubtotals[date] ?? { bet: 0, win: 0 }
  const historiesId = groupedHistoryId[date]
  const groupedHistories = histories.filter(({ id }) => historiesId.find((historyId) => historyId === id))

  const [expanded, setExpanded] = useState(true)
  const groupRef = useRef<HTMLDivElement | null>(null)

  const toggleGroup = () => {
    playSound(['click'])
    setExpanded((prev) => {
      const isExpanded = !prev
      if (groupRef.current) {
        const heightInRem = groupRef.current.scrollHeight / rootEmFontSize
        groupRef.current.style.height = isExpanded ? `${heightInRem}rem` : '0'
      }
      return isExpanded
    })
  }

  useEffect(() => {
    if (groupRef.current && expanded) {
      const heightInRem = groupRef.current.scrollHeight / rootEmFontSize
      groupRef.current.style.height = `${heightInRem}rem`
    }
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [expanded, historiesId])

  return (
    <div className="pb-[0.75rem]">
      <div
        className="groupHeader gridRow cursor-pointer"
        onClick={toggleGroup}
      >
        <div className="gridCell col-span-2">
          <div className="flex w-[47%] justify-between">
            <div>{date}</div>
            <div className={`arrowIcon ${expanded ? 'expanded' : 'collapsed'}`}>
              <Icon.UpArrow className="h-3 w-3" />
            </div>
          </div>
        </div>
        {/* Take the first entry's currency as the currency for the total value (FND-809, at the time of writing, the BE doesn't return the currency of the group) */}
        <div className="gridCell">{currencyFormat(bet, histories[0].currency)}</div>
        <GameHistoryResult
          className="gridCell"
          status={histories[0].status}
          result={win - bet}
          currency={histories[0].currency}
        />
      </div>
      <div
        className={`groupContent ${expanded ? 'expanded' : 'collapsed'}`}
        ref={groupRef}
      >
        {groupedHistories.map((history, idx) => (
          <GroupItem
            key={idx}
            history={history}
            collapsed={!expanded}
          />
        ))}
      </div>
    </div>
  )
}

export default memo(DateGroup)
