import { GameAction } from '@ikigaians/connect'
import { GameType } from '@ikigaians/house'
import { AnimatePresence, motion } from 'framer-motion'
import { memo, useEffect, useRef, useState } from 'react'
import { useTranslation } from 'react-i18next'

import RouletteBubbleGameResult from '@repo/ui/components/roulette/RouletteBubbleGameResult'
import SicboBubbleGameResult from '@repo/ui/components/sicbo/SicboBubbleGameResult'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { RoomType, useChatStore } from '@repo/ui/store/useChatStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useSettingStore } from '@repo/ui/store/useSettingStore'
import { isRouletteGameType, isSicboGameType } from '@repo/ui/utils/common'
import { cn, isDesktop } from '@repo/utils/common/common'
import usePreventPullToRefresh from '@repo/utils/hook/usePreventPullToRefresh'
import { connectIns } from '@repo/utils/socket'

import { ChatBubble } from './ChatBubble'
import ChatInput from './ChatInput'
import './ChatRoom.scss'

// for QA testing
declare global {
  interface Window {
    chat: {
      isBanned: (isBanned?: boolean) => void
      isTableClosed: (isTableClosed?: boolean) => void
    }
    sound: {
      mute: (sound: string[]) => void
      unMute: (sound: string[]) => void
    }
  }
}

const GAME_TABLE = useConfigStore.getState().GAME_TABLE

export function ChatHeader() {
  const { t } = useTranslation()
  const dealer = useBettingStore((state) => state.dealer)
  return (
    <div className="chatRoomHeader center absolute left-1/2 top-[1.9rem] -translate-x-1/2 flex-col gap-2">
      <div className="tracking-[0.113rem]">{t('COMMON.CHAT')}</div>
      <div className="flex gap-[0.125rem] text-xs leading-4 tracking-[0.03rem]">
        <div>{t('COMMON.GAME_PRESENTER')}:</div>
        <div className="chatTitleName">{dealer.name}</div>
      </div>
    </div>
  )
}

function ChatBannedBanner() {
  const { t } = useTranslation()
  return (
    <div className="box error">
      <h1 className="error">{t('COMMON.BANNED')}</h1>
      <p className="error">{t('COMMON.BANNED_TIP')}</p>
    </div>
  )
}

function ChatDisabledBanner() {
  const { t } = useTranslation()
  return (
    <div className="box">
      <h1>{t('COMMON.CHAT_DISABLED')}</h1>
      <p className="px-10">{t('COMMON.CHAT_DISABLED_TIP')}</p>
    </div>
  )
}

function TableClosedBanner() {
  const { t } = useTranslation()
  return (
    <div className="box my-3">
      <h1>{t('COMMON.TABLE_CLOSED')}</h1>
      <span>
        <p>{t('COMMON.TABLE_CLOSED_TIP1')}</p>
        <p>{t('COMMON.TABLE_CLOSED_TIP2')}</p>
      </span>
    </div>
  )
}

interface ChatRoomProps {
  type: RoomType
  desktopLive?: boolean
  isLayerHover?: boolean
  isInputHasText?: boolean
  setIsInputHasText?: (isInputHasText: boolean) => void
  setIsInputHover?: (isHover: boolean) => void
}

function ChatRoom(props: ChatRoomProps) {
  const { type, desktopLive, isLayerHover, isInputHasText } = props
  const remainedBalance = useCartStore((state) => state.remainedBalance)
  const isBanned = useChatStore((state) => state.isBanned)
  const isTableClosed = useChatStore((state) => state.isTableClosed)
  const isChatDisabled = remainedBalance === 0
  const [isInitialLoadFinish, setIsInitialLoadFinish] = useState(false)

  const scrollWrapRef = useRef<HTMLDivElement>(null)
  usePreventPullToRefresh(scrollWrapRef)

  useEffect(() => {
    const loadChatHistory = () => {
      try {
        if (type === RoomType.Live) {
          connectIns.listGameChatHistory(GAME_TABLE)
        } else {
          connectIns.listSupportChatHistory()
        }
      } catch (error) {
        console.error('Failed to load chat history', error)
      }
    }

    loadChatHistory()

    return () => {
      if (type === RoomType.Support) {
        try {
          connectIns.send(GameAction.UNSUBSCRIBE_SUPPORT)
        } catch (error) {
          console.error('Failed to unsubscribe chat support', error)
        }
      }
    }
  }, [type])

  const queueLive = useChatStore((state) => state.queueLive)
  const queueSupport = useChatStore((state) => state.queueSupport)
  const isOtherMsgEnabled = useSettingStore((state) => state.otherMessagesEnable)
  // filter out others messages if setting is disabled
  const filteredQueueLive = isOtherMsgEnabled ? queueLive : queueLive.filter((msg) => msg.sender?.actor !== 'others')
  const renderType = type === RoomType.Live ? filteredQueueLive : queueSupport
  const renderList = [...renderType].reverse()

  window.chat = {
    isBanned: (isBanned: boolean = true) => {
      useChatStore.setState({ isBanned })
    },
    isTableClosed: (isTableClosed: boolean = true) => {
      useChatStore.setState({ isTableClosed })
    },
  }

  useEffect(() => {
    if (!renderList.length || isInitialLoadFinish) return
    setIsInitialLoadFinish(true)
  }, [renderList, type, isInitialLoadFinish])

  return (
    <div
      id="ChatRoom"
      className={cn('absolute flex h-full w-full flex-col-reverse gap-3 px-4 pb-4')}
    >
      <div className={cn('shadowHint', { isDesktop, desktopLive })}></div>

      {isChatDisabled ? <ChatDisabledBanner /> : isBanned ? <ChatBannedBanner /> : <ChatInput {...props} />}
      {isTableClosed && <TableClosedBanner />}
      <motion.div
        ref={scrollWrapRef}
        className="g-noBar chatRoomContent scrollWrap flex flex-col-reverse pt-4"
        layout={isInitialLoadFinish && desktopLive && 'position'}
      >
        <AnimatePresence>
          {renderList.map((data, idx) => {
            const { content } = data
            const { type } = content
            if (type !== 'text' && 'gameType' in content) {
              const gameType = content.gameType as GameType
              if (isRouletteGameType(gameType))
                return (
                  <RouletteBubbleGameResult
                    key={idx}
                    data={data}
                  />
                )
              if (isSicboGameType(gameType))
                return (
                  <SicboBubbleGameResult
                    key={idx}
                    data={data}
                  />
                )
            }

            const userId = data.sender?.userId
            const isLastBubbleTimestamp = !(idx > 0 && renderList[idx - 1].sender?.userId === userId) && type !== 'gameHistory'
            const isLastThree = idx < 3
            const isFirstBubble = !(idx !== renderList.length - 1 && renderList[idx + 1].sender?.userId === userId)

            return (
              <ChatBubble
                key={`chat-${data.timestamp}-${data.sender?.userId || idx}`}
                data={data}
                desktopLive={desktopLive}
                isFirstBubble={isFirstBubble}
                isLastBubbleTimestamp={isLastBubbleTimestamp}
                isLastThree={isLastThree}
                isInitialLoadFinish={isInitialLoadFinish}
                isLayerHover={isLayerHover}
                isInputHasText={isInputHasText}
              />
            )
          })}
        </AnimatePresence>
      </motion.div>
    </div>
  )
}

export default memo(ChatRoom)
