import { getChipSrc } from '@repo/ui/store/utils/chips'
import { cn } from '@repo/utils/common/common'
import { memo } from 'react'

type Props = {
  val: number
  isDisabled: boolean
  className?: string
  [key: string]: unknown
}

export const ChipImg = memo(({ val, isDisabled, ...props }: Props) => {
  const { front, back } = getChipSrc(val, isDisabled)

  const alt = `Chip image for value ${val}`

  return (
    <div
      className={cn('chipImg')}
      {...props}
    >
      <img
        src={back}
        alt={alt}
        className="chipImgBack"
      />
      <img
        src={front}
        alt={alt}
        className="chipImgFront"
      />
    </div>
  )
})

ChipImg.displayName = 'ChipImg'

export default ChipImg
