import { MessageHistoryPayload } from '@ikigaians/house'
import { motion } from 'framer-motion'
import { useEffect, useRef, useState } from 'react'

import { cn } from '@repo/utils/common/common'

// Move utility function outside component for better organization
const formatTime = (timestamp: number): string => {
  return new Date(timestamp).toLocaleString('en-US', {
    hour: '2-digit',
    minute: '2-digit',
    hour12: false,
  })
}

interface ChatBubbleProps {
  data: MessageHistoryPayload
  isFirstBubble: boolean
  isLastBubbleTimestamp: boolean
  isInitialLoadFinish?: boolean
  desktopLive?: boolean
  isLastThree?: boolean
  isLayerHover?: boolean
  isInputHasText?: boolean
}

const MIN_PERIOD_BEFORE_HIDDEN = 3 * 1000

/**
 * Every message will be alive for 24 seconds,
 * and delegate the last three seconds timeout handling to CSS,
 * making that after un-hovering, the message will be hidden after a LEAST 3 seconds
 * (opacity = 1 -> 0, transition-delay: 3s).
 */
const TTL = 24 * 1000 - MIN_PERIOD_BEFORE_HIDDEN

export function ChatBubble({
  data,
  desktopLive,
  isFirstBubble,
  isLastBubbleTimestamp,
  isLastThree,
  isInitialLoadFinish,
  isLayerHover,
  isInputHasText,
}: ChatBubbleProps) {
  const { timestamp, content, sender } = data
  const [isHover, setIsHover] = useState(false)
  const [isFresh, setIsFresh] = useState(true)
  const isAlive = isFresh && isInitialLoadFinish && isLastThree

  const ttlRef = useRef<number>(TTL)

  const displayName = sender?.username ?? ''
  const messageText = 'text' in content ? content.text : ''
  const isCurrentUser = sender?.actor === 'me'

  useEffect(() => {
    let interval: ReturnType<typeof setInterval> | null = null

    if (!isInitialLoadFinish || !desktopLive || !isLastThree) return setIsFresh(false)

    if (isLayerHover || isInputHasText) return

    interval = setInterval(() => {
      ttlRef.current -= 1000
      if (ttlRef.current <= 0) {
        setIsFresh(false)
        if (interval) clearInterval(interval)
      }
    }, 1000)

    return () => {
      if (interval) clearInterval(interval)
    }
  }, [isInitialLoadFinish, isLayerHover, isInputHasText, isLastThree, desktopLive])

  return (
    <motion.div
      className={cn('ChatBubble mb-1', { isAlive }, { isSelf: isCurrentUser })}
      layout="position"
      initial={{ opacity: 0, translateY: '100%', scale: 1.1 }}
      animate={{ opacity: 1, translateY: 0, scale: 1 }}
      transition={
        isInitialLoadFinish && desktopLive ? { type: 'spring', stiffness: 100, damping: 15, mass: 1, duration: 0.8 } : { duration: 0 }
      }
    >
      <div
        className="msg break-words break-keep"
        onMouseEnter={() => setIsHover(true)}
        onMouseLeave={() => setIsHover(false)}
      >
        {desktopLive && (
          <motion.div
            className="timer desktopLiveTimer mt-[.125rem] opacity-0"
            animate={{ opacity: isHover ? 1 : 0 }}
            transition={{ duration: 0.3 }}
          >
            {formatTime(timestamp)}
          </motion.div>
        )}
        {isFirstBubble && <div className="name">{displayName}</div>}
        {messageText}
      </div>
      {isLastBubbleTimestamp && !desktopLive && <div className="timer mt-[.125rem]">{formatTime(timestamp)}</div>}
    </motion.div>
  )
}
