import { AnimatePresence, motion } from 'framer-motion'
import { useTranslation } from 'react-i18next'

import { Icon } from '@repo/ui/icon'
import { useToastStore } from '@repo/ui/store/useToastStore'
import { cn, isDesktop } from '@repo/utils/common/common'

import ClickableArea from '../common/clickableArea/ClickableArea'
import './ToastList.scss'

export default function ToastList() {
  const { toastQueue, dismissToast } = useToastStore((s) => ({ toastQueue: s.toastQueue, dismissToast: s.dismissToast }))

  return (
    <div
      id="ToastList"
      className={cn('center absolute left-2 right-2 z-[11] flex-col-reverse md:mx-auto md:w-1/2', { isDesktop })}
    >
      <AnimatePresence>
        {toastQueue.map(({ type, id, msg, persistent }) => (
          <motion.div
            key={id}
            className={cn('toastBubble center mb-2 overflow-hidden', type)}
            layout="position"
            initial={{ opacity: 0, y: -50 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.3, ease: 'linear' }}
          >
            {type === 'success' && <Icon.SuccessCircle className="size-6 shrink-0" />}
            {type === 'error' && <Icon.Error className="size-6 shrink-0" />}
            {type === 'info' && <Icon.Info className="size-6 shrink-0" />}

            <Message msg={msg} />

            {!persistent && (
              <ClickableArea
                className="size-[3.125rem]"
                onClick={() => dismissToast(id)}
              >
                <Icon.Clear className="iconClear size-6 shrink-0" />
              </ClickableArea>
            )}
          </motion.div>
        ))}
      </AnimatePresence>
    </div>
  )
}

function Message({ msg }: { msg: string }) {
  const { t } = useTranslation()
  return <div className="msg mr-auto line-clamp-2">{t(msg)}</div>
}

/*
 * ex.
 * useToastStore.getState().addToast('error', 'COMMON.GAME_PAUSED')
 * useToastStore.getState().addToast('info', 'COMMON.GAME_PAUSED', { persistent: true, id: 'game-paused' })
 * useToastStore.getState().dismissToast('game-paused')
 */
