import SliderBar from '@repo/ui/components/common/slider/SliderBar'
import TabSwitch, { TabListType } from '@repo/ui/components/common/tabSwitch/TabSwitch'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import { statisticTabType, useStatisticStore } from '@roulette/stores/useStatisticStore'
import { useTranslation } from 'react-i18next'

const StatisticFooter = () => {
  const { t } = useTranslation()
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)

  const { activeTab, numberOfRounds } = useStatisticStore((state) => state)

  const tabList: TabListType<statisticTabType> = [
    { id: 'hotCold', label: t('COMMON.HOT_COLD') },
    { id: 'last500', label: t('COMMON.LAST_500') },
    { id: 'advanced', label: t('COMMON.ADVANCED') },
  ]

  return (
    <div
      className={cn('mt-6 px-4', {
        'mt-2': activeTab === 'advanced',
      })}
    >
      <div
        id="NumberOfRounds"
        className={`${isMobileDevice && !isSmallDevice && 'px-4'}`}
      >
        <div className="flex h-full flex-col justify-between gap-[0.9rem]">
          <div className="flex justify-between">
            <span>{t('ROULETTE.NUMBER_OF_ROUNDS')}</span>
            <span>{numberOfRounds}</span>
          </div>
          <div className="slider">
            <SliderBar
              value={numberOfRounds}
              min={50}
              max={500}
              step={10}
              intervalCount={isMobileDevice ? (isSmallDevice ? 42 : 46) : 50}
              onChange={(val) => {
                useStatisticStore.setState({ numberOfRounds: val })
              }}
            />
          </div>
        </div>
      </div>
      <TabSwitch<statisticTabType>
        list={tabList}
        activeTab={activeTab}
        onClick={(tab) => useStatisticStore.setState({ activeTab: tab })}
        wrapperClass="pb-4 pt-6"
        tabContainerClass="h-[2.25rem] pb-[0.2rem] mx-[0.1rem]"
      />
    </div>
  )
}

export default StatisticFooter
