const getLevel = (level: number) => {
  if (level === 0) return 'mute'
  if (level < 0.34) return 'low'
  if (level < 0.67) return 'medium'
  return 'high'
}

type SoundButtonProps = {
  level: number
  onClick?: () => void
  type: SoundButtonType
}

function SoundButton(props: SoundButtonProps) {
  const { level, onClick, type } = props
  const svgLevel = getLevel(level)

  return (
    <div
      id="SoundButton"
      className="center h-8 w-8 cursor-pointer"
      onClick={() => {
        playSound(['click'])
        if (onClick) onClick()
      }}
    >
      <svg
        viewBox="0 0 20 20"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        className="h-5 w-5"
      >
        {svgContent[svgLevel](type)}
      </svg>
    </div>
  )
}

export default memo(SoundButton)
