import { MessageContentGameHistory, MessageHistoryPayload } from '@ikigaians/house'
import { cn, getFormattedRoundId } from '@repo/utils/common/common'
import { useTranslation } from 'react-i18next'

import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { getRouletteNumberColor } from '@repo/ui/utils/roulette'

import { WinNumber } from './RouletteWinNumberResult'
const RouletteBubbleGameResult = (props: { data: MessageHistoryPayload }) => {
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)

  const current = props.data.content as MessageContentGameHistory
  const result = Number(current.result?.['roulette'] ?? -1)
  const color = !current.result || result < 0 ? 'cancel' : getRouletteNumberColor(result)
  const { t } = useTranslation()

  return (
    <div className={cn('g-winNumberFrame relative', color, 'my-3 h-[8.5rem]')}>
      {result >= 0 && (
        <div className="absolute right-1 top-10 w-[12rem]">
          <h3 className={cn(isSmallDevice ? 'ml-[3rem]' : '', 'text-center')}>{t('COMMON.GAME_RESULT')}:</h3>
          <div className="h-2"></div>

          {WinNumber && (
            <div className={cn(isSmallDevice ? 'absolute -bottom-[1.3rem] -right-[1rem] h-full w-[90%]' : '')}>
              <WinNumber win={result} />
            </div>
          )}
        </div>
      )}
      <div className="absolute left-4 top-4">
        <h2>{t('COMMON.LIVE_ROULETTE')}</h2>
        <div className="h-2"></div>
        <div>
          <div className="grid grid-cols-2 gap-x-10">
            <div>
              <h3>{t('COMMON.GAME_RESOLVED')}:</h3>
              <span>
                {current.finishedAt &&
                  new Date(current.finishedAt).toLocaleString('en-US', {
                    year: 'numeric',
                    month: '2-digit',
                    day: '2-digit',
                    hour: '2-digit',
                    minute: '2-digit',
                    second: '2-digit',
                    hour12: true,
                    timeZone: 'UTC',
                  })}
              </span>
            </div>
            {result < 0 && (
              <div>
                <h3>{t('COMMON.GAME_RESULT')}:</h3>
                <span>{t('COMMON.GAME_CANCELED')}</span>
              </div>
            )}
          </div>
          <div className="h-3"></div>
          <div>
            <h3>{t('COMMON.GAME_NUMBER')}:</h3>
            <span>{`#${getFormattedRoundId(current?.roundId ?? '')}`}</span>
          </div>
        </div>
      </div>
    </div>
  )
}

export default RouletteBubbleGameResult
