import maskBg from '@repo/ui/assets/roulette/virtualBackground/maskBg.webp'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import useVisibility from '@repo/utils/hook/useVisibility'

interface RouletteMaskProps {
  isLoading?: boolean
  virtualBackground: string
}

export default function RouletteMask({ isLoading = false, virtualBackground }: RouletteMaskProps) {
  const isVisible = useVisibility()
  const shouldMask = !isLoading && isVisible

  const maskedBackgroundStyle = {
    backgroundImage: `url(${virtualBackground})`,
    maskImage: `url(${maskBg}), linear-gradient(black, black)`,
    opacity: shouldMask ? 1 : 0,
  }

  const fullBackgroundStyle = {
    backgroundImage: `url(${virtualBackground})`,
    opacity: shouldMask ? 0 : 1,
    transition: shouldMask ? 'opacity 0.3s ease-in-out' : 'none',
  }

  return (
    <>
      {/* Masked background (with hollow effect) */}
      <div
        className={cn('rouletteMask', { isMobileDevice })}
        style={maskedBackgroundStyle}
      />
      {/* Full background (no mask) */}
      <div
        className={cn('rouletteMask rouletteMask--full', { isMobileDevice })}
        style={fullBackgroundStyle}
      />
    </>
  )
}
