import { GameType } from '@ikigaians/house'
import GameLimitComponent from '@repo/ui/components/menu/common/game-rules/GameLimitWrapper'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { getPayTable, isRouletteGameType, isSicboGameType } from '@repo/ui/utils/common'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import { useTranslation } from 'react-i18next'
import './GameLimit.scss'

export default function GameLimit() {
  const { t } = useTranslation()
  const currencySymbol = useCartStore((s) => s.currencySymbol)
  const tableMaxPayout = useConfigStore((s) => s.tableMaxPayout)
  const gameType = useConfigStore((s) => s.GAME_TYPE)
  const isSicBo = isSicboGameType(gameType)
  const isRoulette = isRouletteGameType(gameType)
  const payTable = getPayTable(isSicBo ? GameType.AUTO_SIC_BO : isRoulette ? GameType.AUTO_ROULETTE : GameType.BACCARAT)
  const content = (
    <section>
      <table>
        <thead>
          <tr>
            <th>{t('COMMON.BET_TYPE')}</th>
            <th>{t('COMMON.BET_LIMITS')}</th>
            <th>{t('COMMON.PAYOUTS')}</th>
          </tr>
        </thead>
        <tbody>
          {Object.keys(payTable).map((betType, index) => {
            const betTypeEntry = payTable[betType] || {}
            const className = betTypeEntry.className || ''
            const limits = betTypeEntry.limits || {}

            return (
              <tr key={index}>
                <td className={cn(className)}>{betTypeEntry.betType || betType}</td>
                <td className={cn(className)}>
                  {limits.max ? (
                    <>
                      {currencySymbol}
                      {limits.min?.toLocaleString() || '0'} - {limits.max.toLocaleString()}
                    </>
                  ) : (
                    ''
                  )}
                </td>
                <td className={cn(className)}>{betTypeEntry.payouts || ''}</td>
              </tr>
            )
          })}
        </tbody>
      </table>
      <div
        className={cn('maxPayout px-2 py-4', {
          'py-3': isMobileDevice,
        })}
      >
        {t('COMMON.MAX_PAYOUT', {
          currencySymbol,
          amount: (tableMaxPayout || 0).toLocaleString(),
        })}
      </div>
    </section>
  )

  return <GameLimitComponent>{content}</GameLimitComponent>
}
