import { Icon } from '@repo/ui/icon'
import { playSound } from '@repo/utils/common/sound'
import { memo } from 'react'

type LinkElementProps = {
  title: string
  value: string
  onClick: () => void
}

function SettingLink(props: LinkElementProps) {
  const { title, value, onClick } = props
  return (
    <div
      className="row cursor-pointer"
      onClick={() => {
        onClick()
        playSound(['click'])
      }}
    >
      <div>{title}</div>
      <div className="flex items-center">
        <div className="g-text-golden pr-[0.1rem] pt-[0.1rem] text-xs tracking-[.045rem]">{value}</div>
        <Icon.RightGolden className="w-4" />
      </div>
    </div>
  )
}

export default memo(SettingLink)
