import { BetTypeEnum } from '@roulette/types/betType'
import { create } from 'zustand'
import { createJSONStorage, persist, subscribeWithSelector } from 'zustand/middleware'

const statisticTabs = ['hotCold', 'last500', 'advanced'] as const
export type statisticTabType = (typeof statisticTabs)[number]

const statisticSubTabs = ['grid', 'racetrack'] as const
export type statisticSubTabType = (typeof statisticSubTabs)[number]

type StatisticType = {
  numberOfRounds: number
  activeTab: statisticTabType
  activeBoard: statisticSubTabType
  advancedMap: Record<string, number>
}

export const useStatisticStore = create<StatisticType>()(
  subscribeWithSelector(
    persist(
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      (set) => ({
        numberOfRounds: 500,
        activeTab: 'last500',
        activeBoard: 'grid',
        advancedMap: {
          [BetTypeEnum.STRAIGHT_UP_0]: 0,
          [BetTypeEnum.RED]: 0,
          [BetTypeEnum.BLACK]: 0,
          [BetTypeEnum.ODD]: 0,
          [BetTypeEnum.EVEN]: 0,
          [BetTypeEnum.LOW]: 0,
          [BetTypeEnum.HIGH]: 0,
          [BetTypeEnum.DOZEN_1]: 0,
          [BetTypeEnum.DOZEN_13]: 0,
          [BetTypeEnum.DOZEN_25]: 0,
          [BetTypeEnum.COLUMN_1]: 0,
          [BetTypeEnum.COLUMN_2]: 0,
          [BetTypeEnum.COLUMN_3]: 0,

          [BetTypeEnum.TIER]: 0,
          [BetTypeEnum.ORPH]: 0,
          [BetTypeEnum.VOISINS]: 0,
          [BetTypeEnum.ZERO]: 0,
        },
      }),
      {
        name: 'useStatisticStore',
        storage: createJSONStorage(() => localStorage),
        onRehydrateStorage: () => (state) => {
          if (!state) return
          // Check for invalid tabs after rehydration
          const { activeTab = 'last500', activeBoard = 'grid' } = state
          if (!statisticTabs.includes(activeTab)) {
            state.activeTab = 'last500'
          }
          if (!statisticSubTabs.includes(activeBoard)) {
            state.activeBoard = 'grid'
          }
        },
      }
    )
  )
)
