import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { cn } from '@repo/utils/common/common'
import { playSound } from '@repo/utils/common/sound'
import { memo, useLayoutEffect, useState } from 'react'
import { default as ClickableArea } from '../clickableArea/ClickableArea'
import './TabSwitch.scss'

export type TabListType<T> = {
  id: T
  label: string
  disabled?: boolean
}[]

type TabSwitchProps<T> = {
  list: TabListType<T>
  activeTab: T
  onClick: (id: T) => void
  wrapperClass?: string
  tabsBtnContainerClass?: string
  tabContainerClass?: string
  textClass?: string
}

function TabSwitch<T>(props: TabSwitchProps<T>) {
  const { list, activeTab, onClick } = props
  const { wrapperClass, tabsBtnContainerClass, tabContainerClass, textClass } = props

  const [tabPositionX, setTabPositionX] = useState(0)
  const [tabWidth, setTabWidth] = useState(0)
  const [firstTimeRender, setFirstTimeRender] = useState(true)
  const rootEmFontSize = useLayoutStore((s) => s.rootEmFontSize)

  useLayoutEffect(() => {
    const targetTab = document.getElementById(`qualityTab-${String(activeTab)}`)
    const baseTab = document.getElementById(`qualityTab-${String(list[0].id)}`)

    if (!targetTab || !baseTab) return

    setTabPositionX(targetTab.getBoundingClientRect().x - baseTab.getBoundingClientRect().x)
    setTabWidth(targetTab.getBoundingClientRect().width)
  }, [activeTab, list, rootEmFontSize])

  return (
    <div className={wrapperClass}>
      <div className={cn('tabsBtnContainer', tabsBtnContainerClass)}>
        <div className="tabs p-1">
          {list.map(({ id, label, disabled }) => {
            const active = activeTab === id
            return (
              <ClickableArea
                key={String(id)}
                wrapClassName="w-full h-full"
                className="z-[2] h-[125%] w-full"
                onClick={() => {
                  playSound(['click'])
                  onClick(id)
                  setFirstTimeRender(false)
                }}
                id={`qualityTab-${String(id)}`}
              >
                <div className={cn('tabContainer', 'h-[1.74rem]', tabContainerClass, { active, disabled })}>
                  <div className={cn('tab', 'ml-[0.1rem] mt-[0.1rem] text-[.875rem]', textClass)}>{label}</div>
                </div>
              </ClickableArea>
            )
          })}
          <div
            className={cn('activeTabBackground', { firstTimeRender })}
            style={{ width: `${tabWidth + 1}px`, transform: `translateX(${tabPositionX}px)` }}
            // add more width for the tab to make sure the content won't overflow
          >
            <div className={cn('container', 'h-[1.74rem]', tabContainerClass)}>
              <div className={cn('background text-[.875rem]')}></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default memo(TabSwitch) as typeof TabSwitch
