import { cn } from '@repo/utils/common/common'
import { memo } from 'react'

import ClickableArea from '@repo/ui/components/common/clickableArea/ClickableArea'
import Toggle from '@repo/ui/components/common/toggle/Toggle'
import { playSound } from '@repo/utils/common/sound'

type ToggleProps = {
  title: string
  description?: string
  className?: string
  titleClass?: string
  descClass?: string
  checked: boolean
  onChange: () => void
}

const textClassBase = 'text transition-colors duration-300 ease-in-out'

function SettingToggle(props: ToggleProps) {
  const { title, description, className, titleClass, descClass, checked, onChange } = props

  return (
    <div className={cn('row', className)}>
      <div className="flex flex-col items-start justify-center gap-1">
        <div className={cn(textClassBase, 'toggleTitle', titleClass, !checked && 'text-[#7A7A7A]')}>{title}</div>
        <div
          className={cn(textClassBase, 'text-xs', descClass, checked ? 'text-[#A57D4E]' : 'text-[#644C2F]')}
          style={{ letterSpacing: '-0.015rem' }}
        >
          {description}
        </div>
      </div>
      <div className="flex items-center">
        <ClickableArea
          className="size-[3.125rem]"
          onClick={() => {
            playSound(['click'])
            onChange()
          }}
        >
          <Toggle checked={checked} />
        </ClickableArea>
      </div>
    </div>
  )
}

export default memo(SettingToggle)
