import { Icon } from '@repo/ui/icon'
import { cn } from '@repo/utils/common/common'
import { playSound } from '@repo/utils/common/sound'
import { LANGUAGES } from '@repo/utils/locales/constants'
import { changeLanguage } from 'i18next'
import { memo } from 'react'
import { useTranslation } from 'react-i18next'

function SettingLanguage() {
  const { i18n } = useTranslation()
  return (
    <div className="list selectList mx-4 mb-4 mt-3">
      {LANGUAGES.map((item) => (
        <div
          key={item.code}
          className={cn({ checked: item.code === (i18n.resolvedLanguage ?? i18n.language) }, 'row cursor-pointer hover:brightness-125')}
          onClick={() => {
            playSound(['click'])
            changeLanguage(item.code)
          }}
        >
          <div className="text">{item.display}</div>
          <Icon.CheckMark className="checkMark" />
        </div>
      ))}
    </div>
  )
}

export default memo(SettingLanguage)
