import { useEffect } from 'react'

import { getWinNumberStore } from '@repo/ui/store/useWinNumberStore'

import { OUTER_GROUP_MAP } from '@roulette/service/group'
import { useStatisticStore } from '@roulette/stores/useStatisticStore'
import { BetTypeEnum, raceTrackToStraight } from '@roulette/types/betType'

const useWinNumberStore = getWinNumberStore<string>()

const useStatisticAdvanced = () => {
  useEffect(() => {
    const calculateAdvancedMap = () => {
      const { numberOfRounds } = useStatisticStore.getState()
      const { history } = useWinNumberStore.getState()

      const displayHistory = [...history].slice(-numberOfRounds)
      const sum: Record<string, number> = {
        [BetTypeEnum.STRAIGHT_UP_0]: 0,
        [BetTypeEnum.RED]: 0,
        [BetTypeEnum.BLACK]: 0,
        [BetTypeEnum.ODD]: 0,
        [BetTypeEnum.EVEN]: 0,
        [BetTypeEnum.LOW]: 0,
        [BetTypeEnum.HIGH]: 0,
        [BetTypeEnum.DOZEN_1]: 0,
        [BetTypeEnum.DOZEN_13]: 0,
        [BetTypeEnum.DOZEN_25]: 0,
        [BetTypeEnum.COLUMN_1]: 0,
        [BetTypeEnum.COLUMN_2]: 0,
        [BetTypeEnum.COLUMN_3]: 0,
        [BetTypeEnum.TIER]: 0,
        [BetTypeEnum.ORPH]: 0,
        [BetTypeEnum.VOISINS]: 0,
        [BetTypeEnum.ZERO]: 0,
      }

      displayHistory.forEach((str) => {
        const num = Number(str)
        if (num === 0) sum[BetTypeEnum.STRAIGHT_UP_0]++

        Object.entries(OUTER_GROUP_MAP).forEach(([key, arr]) => {
          if (arr.includes(('straight-' + num) as BetTypeEnum)) sum[key]++
        })

        Object.entries(raceTrackToStraight).forEach(([key, arr]) => {
          if (arr.includes(('straight-' + num) as BetTypeEnum)) sum[key]++
        })
      })

      const entriesList = Object.entries(sum).map(([key, val]) => [key, Math.round((val / numberOfRounds) * 100)])

      useStatisticStore.setState({
        advancedMap: Object.fromEntries(entriesList) as Record<string, number>,
      })
    }

    // Subscribe to numberOfRounds
    const unsubRounds = useStatisticStore.subscribe(
      (state) => state.numberOfRounds,
      () => calculateAdvancedMap()
    )

    // Subscribe to history
    const unsubHistory = useWinNumberStore.subscribe((state) => {
      if (state.history) calculateAdvancedMap()
    })
    // const unsubHistory = useWinNumberStore.subscribe(
    //   (state) => state.history,
    //   () => calculateAdvancedMap()
    // )

    // Initial run
    calculateAdvancedMap()

    return () => {
      unsubRounds()
      unsubHistory()
    }
  }, [])
}

export default useStatisticAdvanced
