import toggle from '@repo/ui/assets/setting/toggle.webp'
import { motion } from 'framer-motion'
import { memo, useId } from 'react'
import './Toggle.scss'

interface ToggleProps {
  checked: boolean
}

const animationConfig = {
  type: 'spring',
  duration: 0.45,
  stiffness: 320,
  damping: 26.67,
  mass: 1,
}

function Toggle({ checked }: ToggleProps) {
  const inputId = useId()

  return (
    <label
      id="Toggle"
      htmlFor={inputId}
    >
      <input
        id={inputId}
        type="checkbox"
        checked={checked}
        className="sr-only"
        readOnly
      />
      <div className="slider absolute inset-0"></div>

      <motion.div
        className="sliderBar absolute h-full"
        initial={false}
        transition={animationConfig}
        animate={{
          width: checked ? '100%' : '40%',
        }}
      />
      <motion.img
        src={toggle}
        alt="Toggle control"
        className="toggleControl"
        initial={false}
        transition={animationConfig}
        animate={{
          x: checked ? '1rem' : 0,
          y: '-50%',
          rotate: checked ? 45 : 0,
        }}
      />
    </label>
  )
}

export default memo(Toggle)
