import { useChatStore } from '@repo/ui/store/useChatStore'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import './NotificationHint.scss'

export const NotificationHint = ({ className }: { className: string }) => {
  const supportChatUnread = useChatStore((s) => s.supportChatUnread)

  return supportChatUnread > 0 ? (
    <div className={cn(`notificationHint pointer-events-none ${isMobileDevice ? 'border-[#191b1b]' : 'border-[#020301]'}`, className)}>
      {supportChatUnread > 9 ? '9+' : supportChatUnread}
    </div>
  ) : null
}
