import { useEffect, useRef } from 'react'

import RouletteMask from '@repo/ui/components/video/RouletteMask'
import VideoStream from '@repo/ui/components/video/VideoStream'
import { isMobileDevice } from '@repo/utils/common/common'

import '@repo/ui/components/video/VideoStream.scss'
import './VideoStream.scss'

import { virtualBackground } from '@roulette/utils/resources/preloadResources'

// Custom hook for Roulette's stream repositioning logic
function useRouletteRepositioning() {
  const containerRef = useRef<HTMLDivElement | null>(null)
  const videoWrapperRef = useRef<HTMLDivElement | null>(null)

  useEffect(() => {
    const tip = document.querySelector('[data-stream-tip]')
    const boundary = document.querySelector('[data-stream-boundary]')
    const streamPlayer = document.getElementById('StreamPlayer')
    const container = containerRef.current
    const videoWrapper = videoWrapperRef.current

    if (!tip || !boundary || !container || !videoWrapper || !isMobileDevice || !streamPlayer) return

    const reposition = () => {
      const { height: vh, width: vw, top: vt } = videoWrapper.getBoundingClientRect()
      const { top: tt } = tip.getBoundingClientRect()
      const { top: bt } = boundary.getBoundingClientRect()

      /** Ratio that video top border goes out of the container top border, and should start shrinking the video's height according to the at - vt */
      const BREAKPOINT_RATIO_TOP = 1.7

      const ratio = vh / vw
      const isCrossTop = ratio < BREAKPOINT_RATIO_TOP
      const MULTIPLIER_CROSS_TOP = 1

      const scale: number = 1.125
      let transformY: number
      let height: string

      if (isCrossTop) {
        // tip and boundary has no space in between, shrink the height to make it align
        height = `${(tt - vt) * MULTIPLIER_CROSS_TOP}px`
        transformY = 0
      } else {
        // tip and boundary has space in between, make video centered between the tip and the main board's top edge
        height = 'unset'
        transformY = (tt - bt) / 2
      }

      container.style.height = height
      container.style.transform = `translateY(${transformY}px) scale(${scale})`
      ;(boundary as HTMLElement).style.transform = `translateY(${-transformY}px)`
    }

    const mutationObserver = new MutationObserver(reposition)
    const resizeObserver = new ResizeObserver(reposition)

    /*
     * Use multiple observers to avoid race conditions:
     *   1. Observe when the stream canvas is appended, so we can resize it immediately.
     *   2. Observe the stream player container because since other factors might resize the player canvas later
     *   3. Observe the videoWrapper (the parent of the stream player container) to respond to viewport size changes.
     *
     * Structure:
     *
     * videoWrapper
     * streamContainer
     *     stream player container
     *        stream canvas
     */

    mutationObserver.observe(streamPlayer, { childList: true })
    resizeObserver.observe(streamPlayer)
    resizeObserver.observe(videoWrapper)

    reposition()

    return () => {
      resizeObserver.disconnect()
      mutationObserver.disconnect()
    }
  }, [])

  return { containerRef, videoWrapperRef }
}

export default function RouletteVideoStream() {
  const renderMode = isMobileDevice ? 1 : 0
  const repositioningRefs = useRouletteRepositioning()

  // Create mask component function that receives isLoading state
  const maskComponent = (isLoading: boolean) => (
    <RouletteMask
      isLoading={isLoading}
      virtualBackground={virtualBackground}
    />
  )

  return (
    <VideoStream
      renderMode={renderMode}
      customTransforms={!isMobileDevice ? `-top-[1.875rem] scale-[0.92]` : ''} // Roulette Desktop - stream up (ARO-252)
      maskComponent={maskComponent}
      streamPlayerChildren={
        <span
          data-stream-boundary
          className="pointer-events-none absolute bottom-[19%] left-0 w-full"
        />
      }
      repositioningHook={() => repositioningRefs}
    />
  )
}
