import Image from '@repo/ui/components/common/Image'
import { COUNT_DOWN_DELAY_SEC, ONE_FRAME_TIME_SEC } from '@repo/ui/constants/animation'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useTimerStore } from '@repo/ui/store/useTimerStore'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import { memo, useEffect, useRef } from 'react'

import initInterpolationFunc, { InterpolationMode } from '@repo/ui/pixi-react/utils/initInterpolationFunc'

import {
  initCapMorphKeyFrame,
  initCapPositionKeyFrame,
  initDesktopCapCentralMorphKeyFrame,
  initGreenKeyFrame,
  initMobileCapCentralMorphKeyFrame,
  initRedKeyFrame,
  initYellowKeyFrame,
} from '../animation'

import mobileCapCentralGreen from '@repo/ui/assets/cap/mobile/cap-central-green.webp'
import mobileCapCentralRed from '@repo/ui/assets/cap/mobile/cap-central-red.webp'
import mobileCapCentralYellow from '@repo/ui/assets/cap/mobile/cap-central-yellow.webp'
import mobileCapLeftGreen from '@repo/ui/assets/cap/mobile/cap-left-green.webp'
import mobileCapLeftRed from '@repo/ui/assets/cap/mobile/cap-left-red.webp'
import mobileCapLeftYellow from '@repo/ui/assets/cap/mobile/cap-left-yellow.webp'
import mobileCapRightGreen from '@repo/ui/assets/cap/mobile/cap-right-green.webp'
import mobileCapRightRed from '@repo/ui/assets/cap/mobile/cap-right-red.webp'
import mobileCapRightYellow from '@repo/ui/assets/cap/mobile/cap-right-yellow.webp'

import desktopCapCentralGreen from '@repo/ui/assets/cap/desktop/cap-central-green.webp'
import desktopCapCentralRed from '@repo/ui/assets/cap/desktop/cap-central-red.webp'
import desktopCapCentralYellow from '@repo/ui/assets/cap/desktop/cap-central-yellow.webp'
import desktopCapLeftGreen from '@repo/ui/assets/cap/desktop/cap-left-green.webp'
import desktopCapLeftRed from '@repo/ui/assets/cap/desktop/cap-left-red.webp'
import desktopCapLeftYellow from '@repo/ui/assets/cap/desktop/cap-left-yellow.webp'
import desktopCapRightGreen from '@repo/ui/assets/cap/desktop/cap-right-green.webp'
import desktopCapRightRed from '@repo/ui/assets/cap/desktop/cap-right-red.webp'
import desktopCapRightYellow from '@repo/ui/assets/cap/desktop/cap-right-yellow.webp'

import type { CapColor, CapRefs } from '../types'
import './Cap.scss'

const CAP_CENTRAL_MORPH_KEY_FRAMES = isMobileDevice
  ? initMobileCapCentralMorphKeyFrame(ONE_FRAME_TIME_SEC)
  : initDesktopCapCentralMorphKeyFrame(ONE_FRAME_TIME_SEC)

const CAP_POSITION_KEY_FRAMES = initCapPositionKeyFrame(ONE_FRAME_TIME_SEC)
const CAP_MORPH_KEY_FRAMES = initCapMorphKeyFrame(ONE_FRAME_TIME_SEC)
const GREEN_KEY_FRAMES = initGreenKeyFrame(ONE_FRAME_TIME_SEC)
const YELLOW_KEY_FRAMES = initYellowKeyFrame(ONE_FRAME_TIME_SEC)
const RED_KEY_FRAMES = initRedKeyFrame(ONE_FRAME_TIME_SEC)

const assetMap = {
  green: {
    left: isMobileDevice ? mobileCapLeftGreen : desktopCapLeftGreen,
    central: isMobileDevice ? mobileCapCentralGreen : desktopCapCentralGreen,
    right: isMobileDevice ? mobileCapRightGreen : desktopCapRightGreen,
  },
  yellow: {
    left: isMobileDevice ? mobileCapLeftYellow : desktopCapLeftYellow,
    central: isMobileDevice ? mobileCapCentralYellow : desktopCapCentralYellow,
    right: isMobileDevice ? mobileCapRightYellow : desktopCapRightYellow,
  },
  red: {
    left: isMobileDevice ? mobileCapLeftRed : desktopCapLeftRed,
    central: isMobileDevice ? mobileCapCentralRed : desktopCapCentralRed,
    right: isMobileDevice ? mobileCapRightRed : desktopCapRightRed,
  },
}

const capMorph = initInterpolationFunc(InterpolationMode.CAP_MORPH)
const linear = initInterpolationFunc(InterpolationMode.LINEAR)

function Cap({ pFactor, nFactor }: { pFactor: number; nFactor: number }) {
  const wrapperRef = useRef<HTMLDivElement>(null)
  const greenCapRef = useRef<HTMLDivElement>(null)
  const yellowCapRef = useRef<HTMLDivElement>(null)
  const redCapRef = useRef<HTMLDivElement>(null)

  const capLeftRefs = useRef<CapRefs>({} as CapRefs)
  const capCentralRefs = useRef<CapRefs>({} as CapRefs)
  const capRightRefs = useRef<CapRefs>({} as CapRefs)

  const capConfig = {
    colors: ['yellow', 'red', 'green'],
    positions: ['left', 'central', 'right'],
    refs: { left: capLeftRefs, central: capCentralRefs, right: capRightRefs },
    containerRef: { yellow: yellowCapRef, red: redCapRef, green: greenCapRef },
  }

  const renderCap = (currentMs: number) => {
    const { betEnd, betStart } = useBettingStore.getState()
    const elapsed = (currentMs - (betEnd - 20000)) * 0.001 - COUNT_DOWN_DELAY_SEC
    const elapsedFromStart = (currentMs - betStart) * 0.001

    /*
     * Consider the first second (0 to 30 frames) as the entry anim
     */
    const isEntry = elapsedFromStart <= 1
    const wrapper = wrapperRef.current

    const splitMap = [
      { cap: yellowCapRef, keyFrames: YELLOW_KEY_FRAMES, color: 'yellow' },
      { cap: redCapRef, keyFrames: RED_KEY_FRAMES, color: 'red' },
      { cap: greenCapRef, keyFrames: GREEN_KEY_FRAMES, color: 'green' },
    ]

    for (const { cap, keyFrames, color } of splitMap) {
      if (!wrapper || !cap.current) continue

      const singleAlpha = linear(elapsed, keyFrames)
      const capCentralMorph = capMorph(elapsed, CAP_CENTRAL_MORPH_KEY_FRAMES(pFactor, nFactor))
      const capAllScale = capMorph(isEntry ? elapsedFromStart : elapsed, CAP_MORPH_KEY_FRAMES)
      const capAllShift = capMorph(isEntry ? elapsedFromStart : elapsed, CAP_POSITION_KEY_FRAMES)

      wrapper.style.opacity = '1'
      wrapper.style.transform = `translateY(${capAllShift}%) scale(${capAllScale})`
      cap.current.style.opacity = singleAlpha.toString()

      const colorKey = color as CapColor
      capCentralRefs.current[colorKey].style.width = `${capCentralMorph}rem`
    }
  }

  useEffect(() => {
    const unSubscribe = useTimerStore.subscribe(
      (state) => state.currentMs,
      (currentMs) => renderCap(currentMs)
    )

    return () => unSubscribe()
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pFactor, nFactor])

  return (
    <div
      id="Cap"
      ref={wrapperRef}
    >
      {capConfig.colors.map((color) => (
        <div
          key={color}
          className="innerCapWrapper"
          ref={capConfig.containerRef[color as keyof typeof capConfig.containerRef]}
        >
          {capConfig.positions.map((position) => (
            <Image
              key={position}
              alt=""
              className={cn({ isMobileDevice, [position]: true, [color]: true })}
              src={assetMap[color as keyof typeof assetMap][position as keyof (typeof assetMap)[keyof typeof assetMap]]}
              ref={(el) => {
                if (el) capConfig.refs[position as keyof typeof capConfig.refs].current[color as keyof CapRefs] = el
              }}
            />
          ))}
        </div>
      ))}
    </div>
  )
}

export default memo(Cap)
