type RaceTrackHighlightsProps = {
  highlights: string[]
}

export const useRaceTrackHighlights = ({ highlights }: RaceTrackHighlightsProps) => {
  const [style, setStyle] = useState('')

  useEffect(() => {
    const cssRules = `${highlights.map((id) => `path[data-v="${id}"], rect[data-v="${id}"]`).join(',')}
      { 
        transition: all 0.4s;
        fill: url('#backgroundGradient');
        stroke: url('#borderGradient');
        stroke-width: 1.5px;
        filter: url('#multiShadow'); 
      }`
    setStyle(cssRules)
  }, [highlights])

  return { style }
}

export const getAddChipParams: GetAddChipParamsFn = (event) => {
  const { pageX: x, pageY: y } = event
  // Prevent same event target on mobile upon pointer down and up
  const element = document.elementFromPoint(x, y) as HTMLSpanElement
  if (!(element instanceof SVGGraphicsElement)) return ''

  const dataV = element.dataset.v
  if (!dataV) return ''

  const isStraight = !raceTrackGroup.includes(dataV as BetTypeEnum)
  const betType = isStraight ? (('straight-' + dataV) as BetTypeEnum) : (dataV as BetTypeEnum)

  return betType
}
