import { cn } from '@repo/utils/common/common'
import { HTMLAttributes, memo, ReactNode } from 'react'
import './ClickableArea.scss'

interface ClickableAreaProps extends HTMLAttributes<HTMLDivElement> {
  children: ReactNode
  className?: string
  wrapClassName?: string
  id?: string
}

function ClickableArea({ children, className, wrapClassName, id, ...props }: ClickableAreaProps) {
  return (
    /**
     * Change center to flex items-center justify-center,
     * to allow for css properties overriding.
     */
    <div
      className={cn(`relative flex items-center justify-center`, wrapClassName)}
      id={id}
    >
      {children}
      <div
        className={cn('clickableArea absolute cursor-pointer', className)}
        {...props}
      />
    </div>
  )
}

export default memo(ClickableArea)
