import { useState } from 'react'

import { useChatStore } from '@repo/ui/store/useChatStore'
import { cn, isDesktop } from '@repo/utils/common/common'

import ClickableArea from '@repo/ui/components/common/clickableArea/ClickableArea'
import ChatSendButtonIcon, { ChatSendButtonState } from './ChatSendButtonIcon'

interface ChatSendButtonProps {
  hasText: boolean
  isFocus: boolean
  desktopLive?: boolean
  onClick?: () => void
}

export default function ChatSendButton({ hasText, isFocus, desktopLive, onClick }: ChatSendButtonProps) {
  const isTableClosed = useChatStore((state) => state.isTableClosed)
  const [isHovered, setIsHovered] = useState(false)

  const isDisabled = isTableClosed
  const isActive = hasText && isFocus && !isDisabled
  const isInactive = hasText && !isFocus && !isDisabled
  const isDefault = !hasText && !isDisabled

  // Determine the current state for the icon
  const getIconState = (): ChatSendButtonState => {
    if (isDisabled) return 'disabled'
    if (isDefault) return 'default'
    if (isInactive) return 'inactive'
    if (isActive && isHovered) return 'hover'
    if (isActive) return 'active'
    return 'default'
  }

  return (
    <ClickableArea
      className="aspect-square size-[150%]"
      onPointerDown={(e: React.PointerEvent) => {
        e.preventDefault()
      }}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      onClick={onClick}
    >
      <button
        className={cn(
          'group relative flex size-11 items-center justify-center rounded-[2.5rem]',
          isDesktop && (isActive || isInactive) ? 'cursor-pointer' : 'cursor-default',
          { 'size-[2.25rem]': desktopLive }
        )}
        disabled={isDisabled}
      >
        <ChatSendButtonIcon
          state={getIconState()}
          className={cn('absolute', desktopLive ? 'size-9' : 'size-11')}
        />
      </button>
    </ClickableArea>
  )
}
