import { Icon } from '@repo/ui/icon'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import { playSound } from '@repo/utils/common/sound'
import { memo } from 'react'
import { useTranslation } from 'react-i18next'

type GameRuleProps = { titleList: string[] }

function GameRule({ titleList }: GameRuleProps) {
  const openDesktopModal = useLayoutStore((state) => state.openDesktopModal)
  const { t } = useTranslation()

  return (
    <div
      className={cn('list mx-4 mb-4 mt-2 cursor-pointer', {
        'mt-3': isMobileDevice,
      })}
      onClick={() => {
        useLayoutStore.setState({ menuPage: 'rules_inner' })
        openDesktopModal('rules_inner')
        playSound(['click'])
      }}
    >
      {titleList.map((item, index) => (
        <div
          key={index}
          className="row"
          onClick={() => useLayoutStore.setState({ scrollToId: item })}
        >
          {t(item)}
          <Icon.RightGolden className="w-4" />
        </div>
      ))}
    </div>
  )
}

export default memo(GameRule)
