import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { scrollWrap } from '@repo/ui/utils/misc'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import React, { memo, useEffect } from 'react'
import './GameRuleInfo.scss'

type GameRuleInfoProps = { children: React.ReactNode }

function GameRuleInfo({ children }: GameRuleInfoProps) {
  const scrollToId = useLayoutStore((state) => state.scrollToId)

  useEffect(() => {
    if (!scrollToId) return
    const animationFrame = scrollWrap({ modalType: 'rules_inner', elementId: scrollToId, offset: isMobileDevice ? 12 : 8 })
    return () => cancelAnimationFrame(animationFrame)
  }, [scrollToId])

  return (
    <div
      id="GameRuleInfo"
      className={cn('px-4 pt-2', {
        'pt-3': isMobileDevice,
      })}
    >
      {children}
    </div>
  )
}

export default memo(GameRuleInfo)
