import ActiveStateWrapper from '@repo/ui/components/common/modal/ActiveStateModal'
import { Tooltip } from '@repo/ui/components/tooltip/Tooltip'
import { cn } from '@repo/utils/common/common'
import { playSound } from '@repo/utils/common/sound'
import { HTMLAttributes } from 'react'
import './SquareButton.scss'

interface SquareButtonProps extends HTMLAttributes<HTMLDivElement> {
  id?: string
  className?: string
  buttonClassName?: string
  child: React.ReactNode
  isGhost?: boolean
  tooltipContent?: string
  tooltipPlacement?: 'top' | 'right' | 'bottom' | 'left'
  selected: boolean
  onClick?: () => void
}

const SquareButton = ({
  id,
  className,
  buttonClassName,
  child,
  isGhost = false,
  tooltipContent = '',
  tooltipPlacement = 'bottom',
  selected = false, // if you don't need the tooltip just set the selected to true
  onClick,
  ...props
}: SquareButtonProps) => {
  return (
    <ActiveStateWrapper>
      <Tooltip
        placement={tooltipPlacement}
        content={tooltipContent}
        className={className}
        selected={selected}
      >
        <div
          id={id}
          className={cn('SquareButton relative h-full w-full', buttonClassName)}
          onClick={() => {
            if (onClick) {
              onClick()
              playSound(['click'])
            }
          }}
          {...props}
        >
          <div className={cn('defaultType', isGhost && 'isGhost')}>{child}</div>

          <div className="hoverType">{child}</div>
        </div>
      </Tooltip>
    </ActiveStateWrapper>
  )
}

export default SquareButton
