import { NormalChip } from '@repo/ui/components/common/chip/NormalChip'
import { WinningChip } from '@repo/ui/components/common/chip/WinningChip'
import { WinningChipRim } from '@repo/ui/components/common/chip/WinningChipRim'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { useWinAnimStore } from '@repo/ui/store/useWinAnimStore'
import { cn } from '@repo/utils/common/common'
import { useEffect, useRef, useState } from 'react'

interface ChipContainerProps {
  chipId: string
  showWin?: boolean
  chipStyle: string
  isSlope?: boolean
  chipTextSize?: number
  chipSkewXDeg?: number
}

export const ChipContainer = ({ chipId, showWin = true, chipStyle, isSlope, chipTextSize, chipSkewXDeg }: ChipContainerProps) => {
  const chipRef = useRef<HTMLDivElement | null>(null)

  const boardHistory = useCartStore((s) => s.boardHistory)
  const currBoard = boardHistory[boardHistory.length - 1] || {}
  const betAmount = currBoard[chipId]

  const latestPlaceBet = useCartStore.getState().latestPlaceBet || {}
  const chipSequence = useBettingStore.getState().chipSequence

  const winResult = useWinAnimStore((s) => s.winResult)
  const winChipValue = winResult?.betDetail?.[chipId]

  const { payout } = winResult || {}
  const winAmount = payout?.[chipId]
  const isWin = winAmount !== undefined && showWin

  const animState = useWinAnimStore((s) => s.animState)
  const runWinAnimation = !!animState

  const [zIndex, setZIndex] = useState<number>(chipSequence)

  const getChipValue = () => {
    if (betAmount > 0) return betAmount
    if (winChipValue && winChipValue > 0) return winChipValue
    return null
  }
  const chipValue = getChipValue()

  useEffect(() => {
    if (Object.keys(latestPlaceBet).includes(chipId)) {
      const newZIndex = useBettingStore.getState().chipSequence + 1
      setZIndex(newZIndex)
      useBettingStore.setState({
        chipSequence: newZIndex,
      })
    }
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [latestPlaceBet])

  return chipValue ? (
    <div
      className={cn(
        'chipContainer pointer-events-none fixed flex items-center justify-center transition-[font-size] duration-300 ease-linear',
        chipStyle
      )}
      style={{ zIndex, fontSize: chipTextSize }}
    >
      <NormalChip
        chipId={chipId}
        value={chipValue}
        isWin={isWin}
        chipRef={chipRef}
        is3D={isSlope}
        chipSkewXDeg={chipSkewXDeg}
      />
      {isWin && runWinAnimation && (
        <>
          <WinningChip
            chipRef={chipRef}
            chipId={chipId}
            value={winAmount}
            is3D={isSlope}
            chipSkewXDeg={chipSkewXDeg}
          />
          <WinningChipRim chipSkewXDeg={chipSkewXDeg} />
        </>
      )}
    </div>
  ) : null
}
