import SliderBar, { SliderBarProps } from '@repo/ui/components/common/slider/SliderBar'
import { JSX, memo } from 'react'

type SliderElementProps = {
  title: string
  value: number
  onChange: (val: number) => void
  className?: string
  prefixElement?: JSX.Element
  sliderProps?: Partial<SliderBarProps>
  intervalCount?: number
}

function SettingSlider(props: SliderElementProps) {
  const { title, value, onChange, className, prefixElement, sliderProps, intervalCount = 33 } = props

  return (
    <div className={`row ${className}`}>
      <div className="flex w-full flex-col items-start gap-2">
        <div className="flex justify-between self-stretch">
          <div>{title}</div>
          <div>{Math.round(value * 100)}</div>
        </div>
        {prefixElement ? (
          <div className="flex w-full items-center justify-between">
            {prefixElement}
            <div className="flex-grow pl-[.75rem]">
              <SliderBar
                value={value}
                min={0}
                max={1}
                step={0.01}
                onChange={onChange}
                intervalCount={intervalCount}
                {...sliderProps}
              />
            </div>
          </div>
        ) : (
          <SliderBar
            value={value}
            onChange={onChange}
          />
        )}
      </div>
    </div>
  )
}

export default memo(SettingSlider)
