import { BetHistoryPayload, GameType } from '@ikigaians/house'
import { Icon } from '@repo/ui/icon'
import { useHistoryStore } from '@repo/ui/store/useHistoryStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { scrollWrapToTop } from '@repo/ui/utils/misc'
import { isMobileDevice } from '@repo/utils/common/common'
import { currencyFormat } from '@repo/utils/common/currency'
import { playSound } from '@repo/utils/common/sound'
import { connectIns, DEFAULT_HISTORY_LIMIT } from '@repo/utils/socket'
import { memo, useEffect, useRef } from 'react'
import GameHistoryResult from './GameHistoryResult'

const getTimeString = (createdAt: number) => {
  const timeOptions: Intl.DateTimeFormatOptions = {
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit',
    hour12: false,
    timeZone: 'UTC',
  }
  return new Date(createdAt).toLocaleString('en-US', timeOptions)
}

const gameTypeStringMap: Record<string, string> = {
  [GameType.AUTO_ROULETTE]: 'Auto Roulette',
  [GameType.ROULETTE]: 'Roulette',
  [GameType.AUTO_SIC_BO]: 'Auto Sic Bo',
  [GameType.SIC_BO]: 'Sic Bo',
  [GameType.BACCARAT]: 'Baccarat',
}

type BetHistoryProps = {
  history: BetHistoryPayload
  collapsed: boolean
}

function GroupItem({ history, collapsed }: BetHistoryProps) {
  const { roundCreatedAt, betTotal, winTotal, gameType, currency, status } = history
  const { histories, next } = useHistoryStore((state) => state)
  const lastHistory = histories.at(-1)
  const isLastItem = lastHistory && lastHistory.id === history.id
  const openDesktopModal = useLayoutStore((state) => state.openDesktopModal)
  const { winMax } = history || {}
  const finalUserWin = winMax && winTotal > winMax ? winMax : winTotal

  const itemRef = useRef<HTMLDivElement | null>(null)
  const intersectionObserver = useRef<IntersectionObserver | null>(null)
  const isFinished = status === 'finished'

  const getMoreHistories = () => {
    if (!next) return
    connectIns?.listBetHistory(DEFAULT_HISTORY_LIMIT, next)
  }

  // Fetch more histories when this is the last item and it's collapsed
  useEffect(() => {
    if (isLastItem && collapsed) {
      getMoreHistories()
    }
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [collapsed, isLastItem])

  // Fetch more histories when this is the last item and it's visible
  useEffect(() => {
    if (!itemRef.current || !isLastItem) return

    if (!intersectionObserver.current) {
      intersectionObserver.current = new IntersectionObserver((entries) => {
        if (entries[0].isIntersecting) {
          getMoreHistories()
        }
      })
    }

    const currentItem = itemRef.current
    const observer = intersectionObserver.current

    if (currentItem) {
      if (isLastItem) {
        observer.observe(currentItem)
      } else {
        observer.unobserve(currentItem)
      }
    }

    return () => {
      if (currentItem && observer) {
        observer.unobserve(currentItem)
      }
    }
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isLastItem])

  return (
    roundCreatedAt && (
      <div
        ref={itemRef}
        className="gridRow"
        onClick={() => {
          useHistoryStore.setState({ currentHistoryContent: history })
          if (isMobileDevice) {
            useLayoutStore.setState({ menuPage: 'history_content' })
          } else {
            openDesktopModal('history_content')
          }
          playSound(['click'])
          scrollWrapToTop('history_content')
        }}
      >
        <div className="gridCell">{getTimeString(roundCreatedAt)}</div>
        <div className="gridCell overflow-hidden truncate whitespace-nowrap">{gameTypeStringMap[gameType]}</div>
        <div className="gridCell">{currencyFormat(betTotal, currency)}</div>
        <div className="gridCell flex items-center justify-end">
          <GameHistoryResult
            status={status}
            result={
              // For cancelled games, show the "0" amount as the result since no win/loss occurred
              isFinished ? finalUserWin - betTotal : 0
            }
            currency={currency}
            className="flex-grow overflow-hidden truncate text-right"
          />
          <Icon.HistoryItemRight className="ml-[.5rem] h-3 w-3 flex-shrink-0" />
        </div>
      </div>
    )
  )
}

export default memo(GroupItem)
