import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useSettingStore } from '@repo/ui/store/useSettingStore'
import { scrollWrapToTop } from '@repo/ui/utils/misc'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import { LANGUAGES } from '@repo/utils/locales/constants'
import { JSX, memo, useMemo } from 'react'
import { useTranslation } from 'react-i18next'
import Audio from './audio/Audio'
import SettingLink from './elements/Link'
import SettingInput from './elements/SettingInput'
import SettingToggle from './elements/SettingToggle'
import VideoTabs from './video/VideoTabs'

type CatalogProps = {
  title: string
  item: JSX.Element
  list?: boolean
}

function Catalog(props: CatalogProps) {
  const { title, item, list } = props
  return (
    <>
      <div className="g-subTitle-2 mb-2 ml-2 mt-1">{title}</div>
      {list ? <div className="list mb-6">{item}</div> : item}
    </>
  )
}

type SettingProps = {
  gameSpecificsElement?: JSX.Element
}

function Setting(props: SettingProps) {
  const { gameSpecificsElement } = props

  const { otherMessagesEnable, toggleOtherMessages } = useSettingStore((s) => ({
    otherMessagesEnable: s.otherMessagesEnable,
    toggleOtherMessages: s.toggleOtherMessages,
  }))
  const openDesktopModal = useLayoutStore((state) => state.openDesktopModal)
  const { t, i18n } = useTranslation()

  const selectedLanguageDisplay = useMemo(
    () => LANGUAGES.find((item) => item.code === (i18n.resolvedLanguage ?? i18n.language))?.name || LANGUAGES[0].name,
    [i18n.resolvedLanguage, i18n.language]
  )

  return (
    <div
      id="setting"
      className={cn('px-4 pt-2', {
        'mt-[-0.275rem]': !isMobileDevice,
      })}
    >
      <Catalog
        title={t('COMMON.VIDEO')}
        item={<VideoTabs />}
      />
      <Catalog
        list
        title={t('COMMON.GENERAL')}
        item={
          <>
            <SettingToggle
              title={t('COMMON.PLAYER_MESSAGES')}
              description={t('COMMON.PLAYER_MESSAGES_TIP')}
              checked={otherMessagesEnable}
              onChange={toggleOtherMessages}
            />
            <SettingInput />
            <SettingLink
              title={t('COMMON.LANGUAGE')}
              value={selectedLanguageDisplay.toUpperCase()}
              onClick={() => {
                if (isMobileDevice) {
                  useLayoutStore.setState({ menuPage: 'language' })
                } else {
                  openDesktopModal('language')
                }
                scrollWrapToTop('language')
              }}
            />
          </>
        }
      />
      {gameSpecificsElement}
      {isMobileDevice && (
        <Catalog
          list
          title={t('COMMON.SOUND')}
          item={<Audio />}
        />
      )}
    </div>
  )
}

export default memo(Setting)
