import { isDesktop, isIOS } from '@repo/utils/common/common'
import { useEffect } from 'react'

/**
 * Prevents pull-to-refresh behavior on mobile devices by managing touch events
 * on the specified DOM element.
 *
 * @param ref - React ref object pointing to the target HTML element that will handle touch events
 */
export default function usePreventPullToRefresh(ref: React.RefObject<HTMLElement | null>) {
  useEffect(() => {
    const target = ref.current

    if (isDesktop || !target) return

    let startX = 0
    let startY = 0

    const handleTouchStart = (e: TouchEvent) => {
      startX = e.touches[0].clientX
      startY = e.touches[0].clientY
    }

    const handleTouchMove = (e: TouchEvent) => {
      if (!isIOS()) {
        e.stopPropagation()
        return
      }

      // Only iOS does boundary judgment
      const currentX = e.touches[0].clientX
      const currentY = e.touches[0].clientY
      const deltaX = currentX - startX
      const deltaY = currentY - startY
      const isHorizontalGesture = Math.abs(deltaX) > Math.abs(deltaY)

      const atTop = target.scrollTop === 0
      const atBottom = target.scrollHeight - target.scrollTop <= target.clientHeight
      const shouldPreventDefault = ((atTop && deltaY > 0) || (atBottom && deltaY < 0)) && !isHorizontalGesture

      if (shouldPreventDefault) {
        e.preventDefault()
      } else {
        e.stopPropagation()
      }
    }

    const handleGlobalTouchMove = (e: TouchEvent) => {
      if (!target.contains(e.target as Node)) {
        e.preventDefault()
      }
    }

    target.addEventListener('touchstart', handleTouchStart, { passive: true })
    target.addEventListener('touchmove', handleTouchMove, { passive: false })
    document.addEventListener('touchmove', handleGlobalTouchMove, { passive: false })

    return () => {
      target.removeEventListener('touchstart', handleTouchStart, { passive: true } as EventListenerOptions)
      target.removeEventListener('touchmove', handleTouchMove, { passive: false } as EventListenerOptions)
      document.removeEventListener('touchmove', handleGlobalTouchMove, { passive: false } as EventListenerOptions)
    }
  }, [ref])
}
