import TabSwitch, { TabListType } from '@repo/ui/components/common/tabSwitch/TabSwitch'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import useStatisticAdvanced from '@roulette/hook/useStatisticAdvanced'
import { statisticSubTabType, useStatisticStore } from '@roulette/stores/useStatisticStore'
import { useTranslation } from 'react-i18next'
import './StatisticAdvanced.scss'
import StatisticsGrid from './StatisticsGrid'
import StatisticsRacetrack from './StatisticsRacetrack'

const StatisticAdvanced = () => {
  const activeBoard = useStatisticStore((state) => state.activeBoard)
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)
  const { t } = useTranslation()
  const tabList: TabListType<statisticSubTabType> = [
    { id: 'grid', label: t('COMMON.BETTING_GRID') },
    { id: 'racetrack', label: t('COMMON.RACETRACK') },
  ]

  useStatisticAdvanced()

  return (
    <div
      id="StatisticAdvanced"
      className={cn({
        'center pb-[2.2rem] pl-[0.1rem]': isMobileDevice,
        'mt-10': isSmallDevice,
      })}
    >
      <div
        className={cn('relative mt-10 h-[12.25rem] w-[20.56rem]', {
          'mt-[8rem] scale-[.81]': isSmallDevice,
        })}
      >
        <StatisticsGrid active={activeBoard === 'grid'} />
        <StatisticsRacetrack active={activeBoard === 'racetrack'} />
      </div>

      <TabSwitch<statisticSubTabType>
        list={tabList}
        activeTab={activeBoard}
        onClick={(tab) => useStatisticStore.setState({ activeBoard: tab })}
        tabsBtnContainerClass={cn('mx-auto mt-[1rem] w-[11.375rem] mb-2', {
          '-mb-1': isMobileDevice,
          'mb-5': isSmallDevice,
        })}
        textClass="text-xs"
      />
    </div>
  )
}

export default StatisticAdvanced
