const centerPos = { x: 112.8, y: 126.2 }
const dotL = { x: 104, y: 32 }
const dotR = { x: 120, y: 32 }

type PolarChartProps = {
  mapping: number[][]
  hotArr: number[]
  coldArr: number[]
}

export default function PolarChart({ mapping, hotArr, coldArr }: PolarChartProps) {
  const renderList = mapToNineStep(mapping)

  const colorType = (result: number, type: string) => {
    const color = hotArr.includes(result) ? 'red' : coldArr.includes(result) ? 'blue' : 'gray'
    if (type === 'inner' && color === 'gray') return '#F9F9F930'

    return `url(#PolarChart_${type}_${color})`
  }

  return (
    <div className="absolute top-0 size-full">
      <svg
        viewBox="0 0 226 251"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        className="size-full"
      >
        {renderList.map((ele, index) => {
          return (
            <g
              key={index}
              style={{ transformOrigin: `${centerPos.x}px ${centerPos.y}px` }}
              transform={`rotate(${9.74 * index})`}
            >
              <path
                d={convertToPath(ele.step)}
                fill={colorType(ele.result, 'inner')}
                className="opacity-60"
              />
              <path
                d={convertToPath(ele.step)}
                stroke={colorType(ele.result, 'stroke')}
                strokeWidth="0.5"
              />
            </g>
          )
        })}
        <SVGColor />
      </svg>
    </div>
  )
}

function mapToNineStep(mapping: number[][]) {
  const numArr = mapping.map(([, value]) => value)
  const min = Math.min(...numArr)
  const max = Math.max(...numArr)
  const range = (max - min) / 9

  return mapping.map(([result, count]) => {
    let step = Math.floor((count - min) / range) + 1
    if (step > 9) step = 9
    return { result, count, step }
  })
}

const convertToPath = (result: number) => {
  const percent = (result / 9) * 0.6 + 0.3

  const _dotL = {
    x: centerPos.x + (dotL.x - centerPos.x) * percent,
    y: centerPos.y + (dotL.y - centerPos.y) * percent,
  }
  const _dotR = {
    x: centerPos.x + (dotR.x - centerPos.x) * percent,
    y: centerPos.y + (dotR.y - centerPos.y) * percent,
  }

  return `M${centerPos.x} ${centerPos.y}L${_dotL.x} ${_dotL.y}L${_dotR.x} ${_dotR.y}Z`
}

const SVGColor = () => (
  <>
    <linearGradient
      id="PolarChart_inner_red"
      gradientUnits="userSpaceOnUse"
    >
      <stop stopColor="#B02525" />
      <stop
        offset="1"
        stopColor="#D94747"
      />
    </linearGradient>
    <linearGradient
      id="PolarChart_inner_blue"
      gradientUnits="userSpaceOnUse"
    >
      <stop stopColor="#4E6EB4" />
      <stop
        offset="1"
        stopColor="#6B86C0"
      />
    </linearGradient>

    <linearGradient
      id="PolarChart_stroke_red"
      gradientUnits="userSpaceOnUse"
    >
      <stop stopColor="#D94747" />
      <stop
        offset="1"
        stopColor="#E58484"
      />
    </linearGradient>
    <linearGradient
      id="PolarChart_stroke_gray"
      gradientUnits="userSpaceOnUse"
    >
      <stop
        stopColor="#555555"
        stopOpacity="0.2"
      />
      <stop
        offset="1"
        stopColor="#B0B0B0"
        stopOpacity="0.2"
      />
    </linearGradient>
    <linearGradient
      id="PolarChart_stroke_blue"
      gradientUnits="userSpaceOnUse"
    >
      <stop stopColor="#4E6EB4" />
      <stop
        offset="1"
        stopColor="#4E6EB4"
      />
    </linearGradient>
  </>
)
