import { motion } from 'framer-motion'
import { useTranslation } from 'react-i18next'

import { useCartStore } from '@repo/ui/store/useCartStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { cn, isMobileDevice, thousandSeparator } from '@repo/utils/common/common'
import { playSound } from '@repo/utils/common/sound'

import playImg from '@repo/ui/assets/autoplay/play.webp'
import stopImg from '@repo/ui/assets/autoplay/stop.webp'
import './AutoPlayCard.scss'

const textVariants = {
  hidden: {
    y: 40,
    opacity: 0,
    transition: {
      type: 'spring',
      stiffness: 500,
      damping: 30,
      duration: 0.3,
    },
  },
  visible: {
    y: 0,
    opacity: 1,
    transition: {
      type: 'spring',
      stiffness: 500,
      damping: 20,
      mass: 1,
      duration: 0.3,
    },
  },
  rollUp: {
    y: -40,
    opacity: 0,
    transition: {
      type: 'spring',
      stiffness: 500,
      damping: 30,
      duration: 0.3,
    },
  },
}

interface AutoPlayCardProps {
  idx: number
  round: number
  selected: boolean
  onClick: (idx: number) => void
  amount: number
}

export default function AutoPlayCard({ idx, round, selected, onClick, amount }: AutoPlayCardProps) {
  const PlayIcon = selected ? stopImg : playImg
  const status = selected ? 'selected' : 'unSelected'
  const { t } = useTranslation()
  const currencySymbol = useCartStore((s) => s.currencySymbol)
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)

  return (
    <motion.div
      id="AutoPlayCard"
      className={cn(
        'h-[7.75rem] w-[3.875rem] flex-shrink-0 cursor-pointer',
        {
          'h-[8.125rem] w-[4.5625rem]': isMobileDevice,
          'h-[8.125rem] w-[3.82813rem]': isMobileDevice && isSmallDevice,
        },
        status
      )}
      onClick={() => {
        playSound(['click'])
        onClick(idx)
      }}
    >
      <div className="inner">
        <div className="count">{round}</div>
        <div className="round">
          <div className="uppercase">{t('COMMON.ROUND', { count: round })}</div>
        </div>
        <div
          className={cn('center relative mb-[0.38rem] h-3', {
            'mb-3': isMobileDevice,
          })}
        >
          <motion.div
            className="left absolute max-w-full overflow-hidden text-ellipsis whitespace-nowrap"
            initial={false}
            animate={selected ? 'rollUp' : 'visible'}
            variants={textVariants}
          >
            {`${currencySymbol}${thousandSeparator(amount)}`}
          </motion.div>
          <motion.div
            className="left absolute"
            initial={false}
            animate={selected ? 'visible' : 'hidden'}
            variants={textVariants}
          >
            <div className="uppercase">{t('COMMON.LEFT')}</div>
          </motion.div>
        </div>
        <motion.div
          className={cn('radioBtn w-[2.875rem]', {
            'w-[3.5625rem]': isMobileDevice,
            'w-[2.82813rem]': isMobileDevice && isSmallDevice,
          })}
          whileTap={{ scale: 0.9 }}
        >
          <img
            src={PlayIcon}
            alt="autoplay stop or play icon"
            className="w-5"
          />
        </motion.div>
      </div>
    </motion.div>
  )
}
