import { BetDetail, capBets, GenericBetNo } from '@ikigaians/house'
import { tooltipEventBus } from '@repo/ui/store/tooltipEvent'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useWinNumberStore } from '@repo/ui/store/useWinNumberStore'
import { addExtraBets, BoardIDEnum, cleanExtraBets, getDraggedChipRef } from '@repo/ui/utils/bet'
import { CallBetsEnum } from '@repo/ui/utils/types/betType.roulette'
import { useEffect } from 'react'
import useChipAction from './useChipAction'

export type FullBetType = GenericBetNo | CallBetsEnum
export type BetsDiff = { normalBets: Record<FullBetType, number>; extraBets: Record<FullBetType, number> }
export type ConvertBetMapFn = (betType: FullBetType, draggedChipVal: number, isDrag: boolean, boardID: BoardIDEnum) => BetsDiff
export type GetAddChipParamsFn = (event: React.MouseEvent<HTMLElement | SVGElement>) => FullBetType | string

interface UseBettingBoardProps {
  convertBetMap: ConvertBetMapFn
  getAddChipParams: GetAddChipParamsFn
  boardID: BoardIDEnum
  extraBetsGroup?: string[]
}

export const useBettingBoard = (props: UseBettingBoardProps) => {
  const boardHistory = useCartStore((s) => s.boardHistory)
  const walletBalance = useCartStore((s) => s.walletBalance)
  const jurisdiction = useConfigStore((s) => s.JURISDICTION)

  const { convertBetMap, getAddChipParams, boardID, extraBetsGroup = [] } = props
  const { payload, dragSession, setDragSession, resetDragSession, onPress, onRelease, draggedChipRef } = useChipAction()

  const draggedChip = draggedChipRef?.current
  const draggedChipVal = Number(draggedChip?.dataset.value) || 0
  const draggedChipType = draggedChip?.dataset.dragChip as FullBetType

  const { tableLimit, betTypesLimit, GAME_TYPE: gameType } = useConfigStore.getState()

  const placeBets = () => {
    if (!payload) return
    const diff = convertBetMap(payload.betType, draggedChipVal, dragSession.isDrag, boardID)

    // Temporarily remove racetrack extra bets from board history
    const { cleanedBets, extraBets } = cleanExtraBets(boardHistory.at(-1) ?? {})

    const capBet = capBets({
      gameType,
      newBets: diff.normalBets as BetDetail,
      original: cleanedBets,
      balance: walletBalance,
      dragSourceBetNo: dragSession.isDrag ? (draggedChipType as GenericBetNo) : undefined,
      betLimit: { tableLimit, betTypesLimit },
      betOptions: jurisdiction.BET_OPTIONS,
    })

    if (capBet.resultBets) {
      // Add racetrack extra bets back
      const finalBets = { ...capBet.resultBets, ...addExtraBets(extraBets, diff.extraBets) }
      useCartStore.setState({
        boardHistory: [...boardHistory, finalBets],
        latestPlaceBet: diff.normalBets,
        playChipSound: true,
      })
    }

    if (capBet.hints) {
      const { isLowBalance, isOverBetTypeMax, isOverTableMax, isOppositeBet } = capBet.hints
      if (isOverBetTypeMax) tooltipEventBus.emit('overLimit', { position: payload.position })
      if (isLowBalance) tooltipEventBus.emit('lowBalance', { position: payload.position })
      if (isOverTableMax) tooltipEventBus.emit('overLimit', { position: payload.position })
      if (isOppositeBet) tooltipEventBus.emit('oppositeBet', { position: payload.position })
      return resetDragSession()
    }
  }

  useEffect(() => {
    if (!payload) return
    placeBets()
    useBettingStore.setState({ resetBoard: false })
    useWinNumberStore.setState({ lastWinCache: 0 })
    resetDragSession()
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [payload])

  const handlePointerDown = <E extends HTMLElement | SVGElement>(e: React.MouseEvent<E>) => {
    const betType = getAddChipParams(e) as FullBetType
    onPress(e, betType, getDraggedChipRef(betType, boardID))
  }

  const handlePointerUp = <E extends HTMLElement | SVGElement>(e: React.MouseEvent<E>) => {
    const betType = getAddChipParams(e) as FullBetType
    onRelease(e, betType, extraBetsGroup)
  }

  return {
    handlePointerDown,
    handlePointerUp,
    dragSession,
    setDragSession,
    resetDragSession,
    draggedChipRef,
  }
}
