import ChipSelectorChip from '@repo/ui/components/chipTray/Chip'
import { useWinAnimStore } from '@repo/ui/store/useWinAnimStore'
import useChipMovePos from '@repo/utils/hook/useChipPos'
import { motion } from 'framer-motion'

interface WinningChipProps {
  chipRef: React.RefObject<HTMLDivElement | null>
  chipId: string
  chipSkewXDeg?: number
  value: number
  is3D?: boolean
}

export const WinningChip = ({ chipRef, chipId, value, is3D, chipSkewXDeg }: WinningChipProps) => {
  const chipMovePos = useChipMovePos(chipRef)
  const animState = useWinAnimStore((s) => s.animState)
  const movingStart = animState === 'winChipsMoving'

  const winChipVariant = {
    initial: { opacity: 0, scale: 1.14, clipPath: 'rect(30% 65% 65% 35%)', x: 0, y: 0, skewX: chipSkewXDeg },
    default: {
      opacity: 1,
      clipPath: 'rect(0% 100% 101% 0%)',
      scale: 1,
      skewX: chipSkewXDeg,
      transition: {
        opacity: {
          delay: 1.133,
          duration: 0.4,
          ease: 'easeInOut',
        },
        clipPath: {
          duration: 0.4,
          ease: 'easeInOut',
          delay: 1.133,
        },
        scale: {
          duration: 0.266,
          ease: 'easeInOut',
          delay: 1.8,
        },
      },
    },
    moveInitial: {
      x: 0,
      y: 0,
      clipPath: 'rect(0% 100% 101% 0%)',
      scale: 1,
      opacity: 1,
      skewX: chipSkewXDeg,
    },
    move: {
      x: chipMovePos.x,
      y: chipMovePos.y,
      skewX: chipSkewXDeg,
      opacity: 0,
      transition: {
        type: 'spring',
        damping: 30,
        duration: 1.25,
        restSpeed: 0.2,
        ease: 'linear',
      },
    },
  }

  return (
    <motion.div
      key={chipId}
      // In order not to be covered by normal chips
      className="pointer-events-none absolute z-[99999] w-full"
      variants={winChipVariant}
      initial={movingStart ? 'moveInitial' : 'initial'}
      animate={movingStart ? 'move' : 'default'}
    >
      <ChipSelectorChip
        value={value}
        isDisabled={false}
        is3D={is3D}
      />
    </motion.div>
  )
}
