import { useDebounce } from '@uidotdev/usehooks'
import { motion } from 'framer-motion'
import { useMemo } from 'react'

import { cn, isMobileDevice } from '@repo/utils/common/common'

export type ChatSendButtonState = 'disabled' | 'default' | 'inactive' | 'active' | 'hover'

interface ChatSendButtonIconProps {
  state: ChatSendButtonState
  className?: string
}

export default function ChatSendButtonIcon({ state, className }: ChatSendButtonIconProps) {
  // Base styles for the container (without transition classes)
  const containerStyles = cn(className)

  // Debounce only between active and hover states for smooth transitions
  const debouncedState = useDebounce(state, 50) // 50ms debounce

  // Use debounced state only for active <-> hover transitions
  const effectiveState = useMemo(() => {
    // Only apply debouncing between active and hover states
    const isActiveHoverTransition = (state === 'active' || state === 'hover') && (debouncedState === 'active' || debouncedState === 'hover')

    if (isActiveHoverTransition) {
      return debouncedState
    }

    // For all other states, use immediate state
    return state
  }, [state, debouncedState])

  // Get opacity based on effective state for framer-motion
  const getOpacity = () => {
    switch (effectiveState) {
      case 'disabled':
        return 0.25
      case 'default':
      case 'inactive':
        return 0.5
      case 'active':
      case 'hover':
        return 1
      default:
        return 1
    }
  }

  // Animation configuration - 150ms ease-out transition specifically for active to hover
  const animationConfig = {
    opacity: getOpacity(),
    transition: {
      duration: 0.15, // 150ms
      ease: 'easeOut',
    },
  }

  // Background gradient colors based on effective state
  const getBackgroundGradient = () => {
    switch (effectiveState) {
      case 'disabled':
        return isMobileDevice
          ? {
              stop1: '#393939',
              stop2: '#242424',
              stroke1: '#686868',
              stroke2: '#313131',
              strokeOffset: 0.42,
            }
          : null
      case 'inactive':
      case 'active':
        return {
          stop1: '#275C3A',
          stop2: '#173723',
          stroke1: '#40975F',
          stroke2: '#1F492E',
          strokeOffset: 1,
        }
      case 'hover':
        return {
          stop1: '#40975F',
          stop2: '#1F492E',
          stroke1: '#40975F',
          stroke2: '#1F492E',
          strokeOffset: 1,
        }
      default:
        return null
    }
  }

  // Arrow fill color based on effective state
  const getArrowFill = () => {
    switch (effectiveState) {
      case 'disabled':
        return '#D8D3C3'
      case 'default':
      case 'inactive':
      case 'active':
      case 'hover':
        return '#FEFEFE'
      default:
        return '#FEFEFE'
    }
  }

  // Arrow opacity based on effective state
  const getArrowOpacity = () => {
    switch (effectiveState) {
      case 'disabled':
        return 0.25
      case 'default':
        return 0.6
      default:
        return 1
    }
  }

  const backgroundGradient = getBackgroundGradient()
  const arrowFill = getArrowFill()
  const arrowOpacity = getArrowOpacity()

  // Use raw state for immediate visibility logic (not debounced)
  const isDesktopArrow = !isMobileDevice && (state === 'default' || state === 'disabled')
  const isMobileArrow = isMobileDevice && state === 'default'
  const shouldHaveBackground = backgroundGradient && (isDesktopArrow || !isMobileArrow)

  return (
    <motion.svg
      viewBox="0 0 44 44"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      className={containerStyles}
      animate={animationConfig}
    >
      {shouldHaveBackground && (
        <>
          {/* Background circle */}
          <motion.path
            d="M22 0.5C33.8741 0.5 43.5 10.1259 43.5 22C43.5 33.8741 33.8741 43.5 22 43.5C10.1259 43.5 0.5 33.8741 0.5 22C0.5 10.1259 10.1259 0.5 22 0.5Z"
            fill={`url(#bg-gradient-${effectiveState})`}
            animate={{ fill: `url(#bg-gradient-${effectiveState})` }}
            transition={{ duration: 0.15, ease: 'easeOut' }}
          />
          {/* Stroke circle */}
          <motion.path
            d="M22 0.5C33.8741 0.5 43.5 10.1259 43.5 22C43.5 33.8741 33.8741 43.5 22 43.5C10.1259 43.5 0.5 33.8741 0.5 22C0.5 10.1259 10.1259 0.5 22 0.5Z"
            stroke={`url(#stroke-gradient-${effectiveState})`}
            animate={{ stroke: `url(#stroke-gradient-${effectiveState})` }}
            transition={{ duration: 0.15, ease: 'easeOut' }}
          />
        </>
      )}

      {/* Arrow icon */}
      <motion.path
        fillRule="evenodd"
        clipRule="evenodd"
        d="M28.6667 22C28.6667 22.2533 28.5263 22.4843 28.3056 22.5942L17.581 27.9347C17.3454 28.052 17.0645 28.0087 16.8722 27.8255C16.6799 27.6422 16.6153 27.3561 16.7092 27.1038L18.6072 22L16.7092 16.8962C16.6153 16.6439 16.6799 16.3578 16.8722 16.1745C17.0645 15.9913 17.3454 15.948 17.581 16.0653L28.3056 21.4058C28.5263 21.5157 28.6667 21.7467 28.6667 22Z"
        initial={{ fill: 'none', fillOpacity: 0 }}
        animate={{
          fill: arrowFill,
          fillOpacity: arrowOpacity,
        }}
        transition={{ duration: 0.15, ease: 'easeOut' }}
      />

      {shouldHaveBackground && (
        <defs>
          {/* Background gradient */}
          <linearGradient
            id={`bg-gradient-${effectiveState}`}
            x1="22"
            y1="0"
            x2="22"
            y2="44"
            gradientUnits="userSpaceOnUse"
          >
            <motion.stop
              animate={{ stopColor: backgroundGradient.stop1 }}
              transition={{ duration: 0.15, ease: 'easeOut' }}
            />
            <motion.stop
              offset="1"
              animate={{ stopColor: backgroundGradient.stop2 }}
              transition={{ duration: 0.15, ease: 'easeOut' }}
            />
          </linearGradient>

          {/* Stroke gradient */}
          <linearGradient
            id={`stroke-gradient-${effectiveState}`}
            x1="22"
            y1="0"
            x2="22"
            y2="44"
            gradientUnits="userSpaceOnUse"
          >
            <motion.stop
              animate={{ stopColor: backgroundGradient.stroke1 }}
              transition={{ duration: 0.15, ease: 'easeOut' }}
            />
            <motion.stop
              offset={backgroundGradient.strokeOffset}
              animate={{ stopColor: backgroundGradient.stroke2 }}
              transition={{ duration: 0.15, ease: 'easeOut' }}
            />
          </linearGradient>
        </defs>
      )}
    </motion.svg>
  )
}
