import ClickableArea from '@repo/ui/components/common/clickableArea/ClickableArea'
import { RoomType, useChatStore } from '@repo/ui/store/useChatStore'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import { playSound } from '@repo/utils/common/sound'
import useVirtualKeyboardOpened from '@repo/utils/hook/useVirtualKeyboardOpened'
import { connectIns } from '@repo/utils/socket'
import React, { memo, useEffect, useRef, useState } from 'react'
import { useTranslation } from 'react-i18next'
import ChatSendButton from './ChatSendButton'

const LIVE_CHAT_MAX_LENGTH = 250

function ChatInput(props: {
  type: RoomType
  desktopLive?: boolean
  setIsInputHover?: (isHover: boolean) => void
  setIsInputHasText?: (hasText: boolean) => void
}) {
  const { t } = useTranslation()
  const { type, setIsInputHover, desktopLive, setIsInputHasText } = props

  const inputRef = useRef<HTMLInputElement>(null)
  const [isFocus, setIsFocus] = useState<boolean>(false)
  const [text, setText] = useState<string>('')
  const [initialized, setInitialized] = useState(false)

  const textLen = text.length
  const isEmpty = textLen === 0
  const maxLength = LIVE_CHAT_MAX_LENGTH

  const isTableClosed = useChatStore((state) => state.isTableClosed)

  useVirtualKeyboardOpened(inputRef)

  const onTextChanged: React.ChangeEventHandler<HTMLInputElement> = (e) => {
    const { value } = e.target
    const clampedText = maxLength ? value.slice(0, maxLength) : value
    setText(clampedText)
    setIsInputHasText?.(clampedText.length > 0)
  }

  const onKeyDown: React.KeyboardEventHandler<HTMLInputElement> = (e) => {
    if (e.key === 'Enter' && !e.nativeEvent.isComposing) send()
  }

  const send = () => {
    // Prevent sending when table is closed or input is empty
    if (isTableClosed || isEmpty) return

    if (type === RoomType.Live) {
      connectIns.sendGameChatText(text)
    } else {
      connectIns.sendSupportChatChatText(text)
    }

    playSound(['click'])
    setText('')
    setIsInputHasText?.(false)
  }

  const renderFloatingLabel = () => {
    if (isEmpty && !isFocus) {
      return t('COMMON.YOUR_MESSAGE')
    } else {
      return `${textLen}/${LIVE_CHAT_MAX_LENGTH}`
    }
  }

  useEffect(() => {
    const inputElem = inputRef.current
    if (!inputElem || isFocus || initialized || desktopLive || isMobileDevice) return

    // Matching with transition of menuWrapBorder in FooterMenu.scss
    setTimeout(() => {
      inputElem.focus()
      setIsFocus(true)
      setInitialized(true)
    }, 200)
  }, [initialized, isFocus, type, desktopLive])

  useEffect(() => {
    return () => {
      setText('')
      setInitialized(false)
    }
  }, [type])

  return (
    <div
      id="ChatInput"
      className="w-full rounded-[2.75rem] p-[0.0625rem]"
      onMouseEnter={() => {
        if (desktopLive) {
          setIsInputHover?.(true)
        }
      }}
      onMouseLeave={() => {
        if (desktopLive) {
          setIsInputHover?.(false)
        }
      }}
    >
      <div
        className={cn('textArea relative', {
          'h-[2.75rem] p-[0rem_0.2rem_0rem_1.125rem]': desktopLive,
          'h-[3.375rem] p-[0.3125rem_0.3125rem_0.3125rem_1.125rem]': !desktopLive,
          focus: isFocus,
        })}
      >
        <ClickableArea
          wrapClassName="static justify-start"
          className="h-full w-full -translate-x-[1.125rem] cursor-text"
          onPointerDown={(e) => e.preventDefault()}
          onClick={() => inputRef.current?.focus()}
        >
          <label>
            <input
              ref={inputRef}
              onChange={onTextChanged}
              onKeyDown={onKeyDown}
              onFocus={() => setIsFocus(true)}
              onBlur={() => setIsFocus(false)}
              maxLength={maxLength}
              value={text}
              className={cn({ 'pt-[0.7rem]': type === RoomType.Live })}
            />
            <span className={cn('absolute left-[1.2rem] top-1/2 -translate-y-1/2', { limit: isFocus || !isEmpty })}>
              {renderFloatingLabel()}
            </span>
          </label>
        </ClickableArea>

        <ChatSendButton
          hasText={!isEmpty}
          isFocus={isFocus}
          desktopLive={desktopLive}
          onClick={send}
        />
      </div>
    </div>
  )
}

export default memo(ChatInput)
