import { ReactNode, useRef } from 'react'

import SpinnerSVG from '@repo/ui/components/common/SpinnerSVG'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import useStream from '@repo/utils/hook/useStream'

import { useBettingStore } from '@repo/ui/store/useBettingStore'
import './VideoStream.scss'

export interface VideoStreamProps {
  renderMode?: number
  customTransforms?: string
  maskComponent?: ReactNode | ((isLoading: boolean) => ReactNode)
  streamPlayerChildren?: ReactNode
  containerChildren?: ReactNode
  wrapperChildren?: ReactNode
  repositioningHook?: () => {
    containerRef: React.RefObject<HTMLDivElement | null>
    videoWrapperRef: React.RefObject<HTMLDivElement | null>
  }
  customSpinnerPosition?: {
    desktop?: string
    mobile?: string
  }
}

interface VideoWrapperProps {
  children: ReactNode
  repositioningRefs?: {
    containerRef: React.RefObject<HTMLDivElement | null>
    videoWrapperRef: React.RefObject<HTMLDivElement | null>
  }
  containerChildren?: ReactNode
  wrapperChildren?: ReactNode
}

function VideoWrapper({ children, repositioningRefs, containerChildren, wrapperChildren }: VideoWrapperProps) {
  const isCanStream = useLayoutStore((s) => s.isCanStream)
  const isSloped = !useBettingStore((state) => state.isBet)
  const defaultContainerRef = useRef<HTMLDivElement | null>(null)
  const defaultVideoWrapperRef = useRef<HTMLDivElement | null>(null)

  // Use provided refs or fallback to default refs
  const containerRef = repositioningRefs?.containerRef || defaultContainerRef
  const videoWrapperRef = repositioningRefs?.videoWrapperRef || defaultVideoWrapperRef

  return (
    <div
      id="VideoWrapper"
      ref={videoWrapperRef}
      className={cn({ isCanStream, isMobileDevice, isSloped })}
    >
      <div
        ref={containerRef}
        className="streamContainer"
      >
        {children}
        {/* Container-level children (e.g., Sicbo upper gradient) */}
        {containerChildren}
      </div>
      {/* Wrapper-level children (e.g., Sicbo lower gradient) */}
      {wrapperChildren}
    </div>
  )
}

export default function VideoStream({
  renderMode,
  customTransforms,
  maskComponent,
  streamPlayerChildren,
  containerChildren,
  wrapperChildren,
  repositioningHook,
  customSpinnerPosition,
}: VideoStreamProps) {
  const defaultRenderMode = renderMode ?? (isMobileDevice ? 1 : 0)
  const { isLoading, setVideoEl, snapshot } = useStream({ renderMode: defaultRenderMode })

  // Call repositioning hook if provided
  const repositioningRefs = repositioningHook?.()

  const getSpinnerClasses = () => {
    const commonCN = 'absolute left-1/2 h-10 w-10 -translate-x-1/2'
    if (customSpinnerPosition) {
      return cn(commonCN, isMobileDevice ? customSpinnerPosition.mobile : customSpinnerPosition.desktop)
    }

    // Default spinner position
    return cn(commonCN, isMobileDevice ? 'top-[38%] w-[1.666rem]' : 'top-[37.5%] w-[3rem]')
  }

  return (
    <VideoWrapper
      repositioningRefs={repositioningRefs}
      containerChildren={containerChildren}
      wrapperChildren={wrapperChildren}
    >
      <div
        id="StreamPlayer"
        ref={setVideoEl}
        className={cn('relative h-full w-full bg-transparent transition-all duration-700 ease-in-out', customTransforms)}
      >
        {/* Stream player children (e.g., Roulette boundary span) */}
        {streamPlayerChildren}

        <div
          className="streamOverlay"
          style={{ backgroundImage: `url(${snapshot})` }}
        />
      </div>

      {/* Render mask component if provided */}
      {typeof maskComponent === 'function' ? maskComponent(isLoading) : maskComponent}

      {/* Loading spinner */}
      {isLoading && (
        <div className="streamOverlay">
          <div className={getSpinnerClasses()}>
            <SpinnerSVG />
          </div>
        </div>
      )}
    </VideoWrapper>
  )
}

// Export the VideoWrapper for cases where games need custom wrapper behavior
export { VideoWrapper }
