import AutoPlayCard from '@repo/ui/components/menu/common/bettingMode/AutoPlayCard'
import { useAutoPlayStore } from '@repo/ui/store/useAutoPlayStore'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import { BetState } from '@repo/utils/types/betstate'
import { useTranslation } from 'react-i18next'

const autoPlayCount = [5, 10, 25, 100]

interface AutoPlayProps {
  requireTitle?: boolean
  closeOnDesktop?: () => void
}

const AutoPlay = ({ requireTitle = true, closeOnDesktop }: AutoPlayProps) => {
  const closeMenu = useLayoutStore((s) => s.closeMenu)
  const roundSelected = useAutoPlayStore((s) => s.roundSelected)
  const totalBet = useCartStore((s) => s.totalBet)
  const { t } = useTranslation()
  const gameState = useBettingStore((s) => s.gameState)

  const boardHistory = useCartStore((s) => s.boardHistory)

  const handleAutoPlay = (idx: number) => {
    const isCancel = roundSelected === idx
    const newSelected = isCancel ? null : idx
    const autoDeductRound = gameState === BetState.Settlement ? 0 : 1

    useAutoPlayStore.setState({
      roundLastChosen: newSelected,
      roundSelected: newSelected,
      roundRemaining: autoPlayCount[idx] - autoDeductRound, // Deduct 1 for the current round
      isAutoPlay: !isCancel,
      boardSnapshot: isCancel ? null : boardHistory[boardHistory.length - 1],
    })

    /*
     * NOTE:
     * On mobile version, the closing behavior of autoPlay component is controlled by useLayoutStore's closeMenu
     * Whereas on desktop, it's controlled from the parent state, since it's not part of the "desktopModal".
     * AutoPlay is in the footer section separately.
     */
    setTimeout(() => {
      if (closeOnDesktop) closeOnDesktop()
      else closeMenu()
    }, 400)
  }

  const subtitle = t('COMMON.AUTOPLAY_PROMPT3')

  const roundRemaining = useAutoPlayStore((state) => state.roundRemaining)

  return (
    <div
      className={cn('flex flex-col gap-4 px-[0.875rem] pb-6 pt-3')}
      id="AutoPlay"
    >
      {requireTitle && <h3>{t('COMMON.AUTOPLAY')}</h3>}
      <div className="subtitle text-center text-[0.75rem] leading-4 tracking-[-0.015rem] text-[#b9b2aa]">{subtitle}</div>
      <div
        className={cn('center flex gap-2', {
          'gap-3': isMobileDevice,
        })}
      >
        {autoPlayCount.map((val, idx) => (
          <AutoPlayCard
            key={idx}
            idx={idx}
            round={idx === roundSelected ? roundRemaining : val}
            amount={totalBet * val}
            selected={idx === roundSelected}
            onClick={handleAutoPlay}
          />
        ))}
      </div>
    </div>
  )
}

export default AutoPlay
