import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { isRouletteGameType } from '@repo/ui/utils/common'
import { cn, isMobileDevice } from '@repo/utils/common/common'

type Props = {
  overlayBg: string
  imageClassName?: string
  className?: string
}

export default function MainBgImage({ overlayBg, imageClassName, className }: Props) {
  // tmp remove background for roulette and uses the video mask, remove later (FND-833)
  const gameType = useConfigStore((s) => s.GAME_TYPE)
  const isCanStream = useLayoutStore((s) => s.isCanStream)
  const isBet = useBettingStore((state) => state.isBet)
  const isRoulette = isRouletteGameType(gameType)
  // const shouldHasBg = !isCanStream

  return !isCanStream ? (
    <div
      id="MainBgImage"
      className={cn(
        'ease-in-outs pointer-events-none absolute top-0 h-full w-full bg-black transition-all duration-700',
        !isMobileDevice && !isBet && !isRoulette && 'translate-y-16 scale-150',
        className
      )}
    >
      {!isRoulette && (
        <img
          src={overlayBg}
          className={cn('overlayBg', imageClassName)}
        />
      )}
    </div>
  ) : null
}
