import { useCartStore } from '@repo/ui/store/useCartStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useLoadingStore } from '@repo/ui/store/useLoadingStore'
import { isSicboGameType } from '@repo/ui/utils/common'
import { convertLabel } from '@repo/utils/common/bet'
import { cn } from '@repo/utils/common/common'
import ChipImg from './ChipImg'

interface ChipProps {
  value: number
  className?: string
  is3D?: boolean
  isActive?: boolean
  isDisabled: boolean
  chipId?: string
  pressChipValue?: number
  chipSkewXDeg?: number
}

const ChipSelectorChip = (props: ChipProps) => {
  const { value, className, is3D, isDisabled, chipId, pressChipValue, chipSkewXDeg } = props

  const isLoadingFinished = useLoadingStore((s) => s.isLoadingFinished)
  const gameType = useConfigStore((state) => state.GAME_TYPE)
  const isSicbo = isSicboGameType(gameType)

  const numValue = convertLabel(value)
  const isPressed = pressChipValue === value

  const handleClick = () => {
    if (isDisabled) return
    useCartStore.getState().setSelectChipVal(value)
    useLayoutStore.setState({ isOpenSelector: false })
  }

  return (
    <button
      className={cn('g-chip', className, { isDisabled, isPressed, isLoadingFinished, isSicbo, is3D })}
      data-value={value}
      onClick={handleClick}
      data-drag-chip={chipId}
      style={{ '--chipSkewXDeg': `${chipSkewXDeg}deg` } as React.CSSProperties}
    >
      <ChipImg
        val={value}
        isDisabled={isDisabled}
      />

      <div className="stroke">{numValue}</div>
      <div className="text">{numValue}</div>
    </button>
  )
}

export default ChipSelectorChip
