import { autoUpdate, FloatingPortal, offset, Placement, shift, useFloating, useHover, useInteractions } from '@floating-ui/react'
import { cn } from '@repo/utils/common/common'
import { AnimatePresence, motion, MotionProps } from 'framer-motion'
import React, { useEffect, useState } from 'react'
import './Tooltip.scss'

type AnimationProps = Pick<MotionProps, 'initial' | 'animate' | 'exit' | 'transition'>

interface TooltipProps {
  placement: Placement
  children: React.ReactNode
  content: string
  className?: string
  selected?: boolean
  animation?: AnimationProps
}

export function Tooltip({ placement, content, children, className, selected, animation }: TooltipProps) {
  const [isOpen, setIsOpen] = useState(false)
  const [isVisible, setIsVisible] = useState(false)

  const { refs, context, x, y, floatingStyles } = useFloating({
    open: isOpen,
    onOpenChange: setIsOpen,
    placement: placement,
    // Make sure the tooltip stays on the screen
    whileElementsMounted: autoUpdate,
    middleware: [offset(8), shift()],
  })

  // Event listeners to change the open state
  const hover = useHover(context, { move: false })

  // Merge all the interactions into prop getters
  const { getReferenceProps, getFloatingProps } = useInteractions([hover])

  const initExitState = getInitExitState(placement)

  const defaultAnim: AnimationProps = {
    initial: initExitState,
    animate: { opacity: 1, x: 0, y: 0 },
    exit: initExitState,
    transition: { duration: 0.2, ease: 'easeOut' },
  }

  const motionProps: AnimationProps = {
    initial: animation?.initial ?? defaultAnim.initial,
    animate: animation?.animate ?? defaultAnim.animate,
    exit: animation?.exit ?? defaultAnim.exit,
    transition: animation?.transition ?? defaultAnim.transition,
  }

  /*
   * This makes sure the framer motion props can get the latest input props,
   * making it more compatible with dynamic animation dependent on states
   */
  useEffect(() => {
    if (isOpen && !selected) setIsVisible(true)
    else setIsVisible(false)
  }, [isOpen, selected])

  return (
    <>
      <div
        ref={refs.setReference}
        {...getReferenceProps()}
        className={cn(className)}
      >
        {children}
      </div>
      <FloatingPortal>
        <AnimatePresence>
          {isVisible && (
            <motion.div
              className={cn('tooltipContainer z-20 whitespace-nowrap', className)}
              ref={refs.setFloating}
              style={{ ...floatingStyles, top: y, left: x }}
              {...getFloatingProps()}
              {...motionProps}
            >
              <div className="tooltip">{content}</div>
              <div className={`tooltipArrow ${positionConvert[placement]}`}></div>
            </motion.div>
          )}
        </AnimatePresence>
      </FloatingPortal>
    </>
  )
}
const positionConvert = {
  top: 'bottom',
  bottom: 'top',
  left: 'right',
  right: 'left',
  'bottom-start': 'bottom',
  'bottom-end': 'top-end',
  'top-start': 'bottom',
  'top-end': 'top',
  'right-start': 'left',
  'right-end': 'right',
  'left-start': 'right',
  'left-end': 'left',
}

const getInitExitState = (placement: Placement) => {
  const baseAnimation = { opacity: 0 }
  const direction = positionConvert[placement]

  const offsetMap = {
    top: { y: '-0.5rem' },
    bottom: { y: '0.5rem' },
    left: { x: '-0.5rem' },
    right: { x: '0.5rem' },
    'top-end': { y: '-0.5rem' },
  } as const

  return { ...baseAnimation, ...offsetMap[direction as keyof typeof offsetMap] }
}
