import { memo, useEffect, useRef, useState } from 'react'

import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { DragSessionType } from '@repo/utils/hook/useChipAction'

import { CallBetsEnum, raceTrackGroup } from '@repo/ui/utils/types/betType.roulette'
import ChipSelectorChip from '../../chipTray/Chip'

interface TrackerChipProps {
  boardId: string
  dragSession: DragSessionType
  draggedChipRef: React.RefObject<HTMLElement | null>
  setDragSession: (dragSession: DragSessionType) => void
  resetDragSession: () => void
}

function TrackerChip(props: TrackerChipProps) {
  const { boardId, dragSession, draggedChipRef, setDragSession, resetDragSession } = props
  const { isDrag, offset, prePosit, currBetType } = dragSession

  const draggedChip = draggedChipRef?.current
  const draggedChipVal = Number(draggedChip?.dataset.value) || 0
  const draggedChipType = draggedChip?.dataset.dragChip

  const chipSequence = useBettingStore((s) => s.chipSequence)
  const [track, setTrack] = useState({ x: 0, y: 0 })
  const boardRef = useRef<HTMLElement | null>(null)
  const trackerRef = useRef<HTMLDivElement | null>(null)

  useEffect(() => {
    boardRef.current = document.getElementById(boardId)
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    const board = boardRef.current
    if (!board || !draggedChip) return

    const onMove = (event: PointerEvent) => {
      if (!draggedChip || raceTrackGroup.includes(draggedChipType as CallBetsEnum)) return
      // If there is no preBetType (dragged chip), don't track the pointer position

      const { left, top } = board.getBoundingClientRect()
      const { pageX: x, pageY: y } = event

      if (!prePosit.x || !prePosit.y) return

      /**
       * Hide the dragged chip only when the tracker chip is rendered,
       * to avoid chip flickering.
       */
      if (trackerRef.current) draggedChip?.style.setProperty('opacity', '0')

      // Calculate the distance moved from the initial position, is not dragging if below threshold of 1px
      // ARO-255
      const deltaX = Math.abs(x - prePosit.x)
      const deltaY = Math.abs(y - prePosit.y)
      const distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY)
      const isDrag = distance >= 1

      setTrack({ x: x - left, y: y - top })
      setDragSession({ ...dragSession, isDrag })
    }

    const onUp = () => {
      if (!currBetType) return resetDragSession()
    }

    /**
     * Prevent scrolling while dragging and interfere with tracking the pointer position
     */
    const onTouchMove = (e: TouchEvent) => {
      if (draggedChip) return e.preventDefault()
    }

    const handlers = [
      { type: 'pointermove', handler: onMove },
      { type: 'pointerup', handler: onUp },
      { type: 'touchmove', handler: onTouchMove, options: { passive: false } },
    ]

    handlers.forEach(({ type, handler, options }) =>
      document.addEventListener(type as keyof DocumentEventMap, handler as EventListener, options)
    )

    return () => {
      handlers.forEach(({ type, handler, options }) =>
        document.removeEventListener(type as keyof DocumentEventMap, handler as EventListener, options as EventListenerOptions)
      )
    }
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [prePosit.x, prePosit.y, draggedChipType, currBetType])

  if (!draggedChip) return null

  // Dynamically grab the dragged chip style and apply on the tracker chip on the fly
  const fontSize = parseFloat(getComputedStyle(draggedChip).fontSize)
  const width = draggedChip?.clientWidth

  if (!isDrag || !fontSize || !width) return null

  return (
    <div
      className="pointer-events-none absolute top-0 h-full w-full"
      ref={trackerRef}
      style={{
        fontSize,
        width,
        left: `${track.x - offset.x}px`,
        top: `${track.y - offset.y}px`,
        // Sicbo's top board has a z-index of 50
        zIndex: chipSequence + 50,
      }}
    >
      <ChipSelectorChip
        value={draggedChipVal}
        isDisabled={false}
      />
    </div>
  )
}

export default memo(TrackerChip)
