import { Icon } from '@repo/ui/icon'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { cn } from '@repo/utils/common/common'
import useVirtualKeyboardOpened from '@repo/utils/hook/useVirtualKeyboardOpened'
import { memo, useCallback, useEffect, useRef, useState } from 'react'
import './MultiCheckInput.scss'

export type Filters = {
  filter: RegExp | ((val: string) => boolean)
  errorMessage: string
}[]

type UpdateResult = {
  success: boolean
  message?: string
}

export type UpdateFunction = (val: string) => UpdateResult | Promise<UpdateResult>

type InputStatus =
  | undefined
  | {
      status: 'success' | 'error'
      message: string
    }

type MultiCheckInputProps = {
  currentValue: string
  placeholder: string
  filters?: Filters
  onUpdate?: UpdateFunction
  modalType?: string
}

function MultiCheckInput(props: MultiCheckInputProps) {
  const { currentValue, placeholder, filters, onUpdate, modalType } = props
  const [value, setValue] = useState(currentValue)
  const [inputStatus, setInputStatus] = useState<InputStatus>()
  const inputRef = useRef<HTMLInputElement>(null)
  const isVirtualKeyboardOpened = useLayoutStore((state) => state.isVirtualKeyboardOpened)
  const rootEmFontSize = useLayoutStore((state) => state.rootEmFontSize)

  useVirtualKeyboardOpened(inputRef)
  useEffect(() => {
    if (!modalType) return

    const modalElement = document.querySelector(`[data-modal-type="${modalType}"]`)
    const scrollWrapElement = (modalElement?.querySelector('.scrollWrap') as HTMLDivElement) || null
    if (!scrollWrapElement) return

    if (inputRef.current && isVirtualKeyboardOpened) {
      // scrollWrap?.scrollTo({top:7.4* rootEmFontSize, behavior: 'smooth'}) might conflict android build-in behavior
      scrollWrapElement?.scrollTo(0, 7.4 * rootEmFontSize)
    }
    if (!isVirtualKeyboardOpened) {
      scrollWrapElement?.scrollTo({ top: 0, behavior: 'smooth' })
    }
  }, [isVirtualKeyboardOpened, modalType, rootEmFontSize])

  const filterCheck = useCallback(
    (text: string): string | undefined => {
      if (!filters) return
      for (const { filter, errorMessage } of filters) {
        const checkPassed = typeof filter === 'function' ? filter(text) : filter.test(text)
        if (!checkPassed) {
          return errorMessage
        }
      }
    },
    [filters]
  )

  const onInputChange: React.ChangeEventHandler<HTMLInputElement> = (ev) => {
    const text = ev.target.value
    const errorMessage = filterCheck(text)

    if (errorMessage) setInputStatus({ status: 'error', message: errorMessage })
    else setInputStatus(undefined)

    setValue(text)
  }

  const updateBtnClick = async () => {
    if (!onUpdate) return
    const errorMessage = filterCheck(value)

    if (errorMessage) {
      setInputStatus({ status: 'error', message: errorMessage })
      return
    }

    const result = isPromise<UpdateResult>(onUpdate) ? await onUpdate(value) : (onUpdate(value) as UpdateResult)
    const status = result.success ? 'success' : 'error'
    const message = result.message || ''
    setInputStatus({ status, message })
  }

  const hasContent = value.length > 0
  const hasError = inputStatus?.status === 'error'
  const isSuccess = inputStatus?.status === 'success'
  const valueChanged = value !== currentValue

  return (
    <div
      id="MultiCheckInput"
      className="w-full"
    >
      <div
        className={cn(
          'input-container',
          hasContent ? 'has-content' : '',
          hasError ? 'error-border' : isSuccess ? 'success-border' : 'default-border'
        )}
      >
        <input
          placeholder=""
          ref={inputRef}
          className="bg-transparent"
          value={value}
          onChange={onInputChange}
        />
        <span className={cn('placeholder', inputStatus?.status ?? 'golden')}>{placeholder}</span>
        {valueChanged && !hasError ? (
          <Icon.SuccessCircleButton
            className="absolute right-[0.3rem] w-[2.5rem] cursor-pointer"
            onClick={updateBtnClick}
          />
        ) : (
          <Icon.SuccessCircleDisabledButton className="absolute right-[0.3rem] w-[2.5rem] cursor-not-allowed" />
        )}
      </div>
      {inputStatus && <div className={cn('message', inputStatus.status)}>{inputStatus.message}</div>}
    </div>
  )
}

export default memo(MultiCheckInput)

function isPromise<T = unknown>(value: unknown): value is Promise<T> {
  return !!value && typeof value === 'object' && typeof (value as Promise<T>).then === 'function'
}
