/**
 * A hook to manage highlighting on a board based on winning numbers.
 *
 * @description
 * This hook handles the logic for highlighting winning numbers and associated areas on a board.
 * It automatically highlights winning numbers when animation starts and clears highlights when it ends.
 *
 * @param args - Configuration options for the board highlighting behavior
 * @param args.isMain - Whether this is the main board (defaults to true if not provided)
 * @param args.lastWin.prefix - Optional prefix to prepend to number identifiers
 * @param args.getInitWinHighlight - Optional function to get initial winning highlights
 * @param args.winHighlightMap - Optional mapping of element IDs to highlight rules
 *                              Each rule can be either an array of numbers (direct match)
 *                              or a function that returns true when given winning numbers should trigger the highlight
 *
 */

import { useEffect, useState } from 'react'

import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useWinAnimStore } from '@repo/ui/store/useWinAnimStore'

type BoardHighlightArgs<T> = {
  isMain?: boolean // Whether the board is the main board
  prefix?: string // Optional prefix for the highlight component, default is ''
  getInitWinHighlight?: (winNumber: T, prefix?: string) => string[] // Function to get initial win highlight
  winHighlightMap: Record<string, (winNums: T) => boolean> // The mapping of win highlights
}

// * T: roulette=string, sicBo=number[], baccarat=string[]
export default function useBoardHighlight<T>(args: BoardHighlightArgs<T>) {
  const [highlights, setHighlights] = useState<string[]>([])
  const { isMain, prefix, getInitWinHighlight, winHighlightMap } = args

  const isBet = useBettingStore((s) => s.isBet)
  const animState = useWinAnimStore((s) => s.animState)
  const winResult = useWinAnimStore((s) => s.winResult)
  const winNums = winResult?.result as T | null
  const isMainBoard = isMain ?? true // Default to true if isMain is not provided

  // Draw the animation when win animation starts
  useEffect(() => {
    const shouldHighlight = animState === 'winAnimationStart'
    if (!isMainBoard || !shouldHighlight || !winNums) {
      setHighlights([])
      return
    }

    // * Highlight the winning numbers, outside bets are included
    const highlight = getInitWinHighlight ? getInitWinHighlight(winNums, prefix) : []

    // Loop through lastWin.numbers and check values in winHighlightMap
    if (winHighlightMap) {
      Object.entries(winHighlightMap).forEach(([k, shouldHighlightFunc]) => {
        const shouldHighlight = shouldHighlightFunc(winNums)
        const hasHighlight = highlight.includes(k)
        if (shouldHighlight && !hasHighlight) highlight.push(k)
      })
    }

    setHighlights(highlight)
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [animState])

  // Clear the highlights when animation ends
  useEffect(() => {
    if (!animState) setHighlights([])
  }, [animState])

  // Clear the highlights when bet starts/ends to prevent hovering highlight stuck on the board
  useEffect(() => {
    if (isBet !== undefined) {
      setHighlights([])
    }
  }, [isBet])

  return { highlights, setHighlights }
}
