import { useCallback, useEffect, useRef, useState } from 'react'

import { tooltipEventBus } from '@repo/ui/store/tooltipEvent'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { getCenterPosition, getDraggedChipEdgeDist } from '@repo/ui/utils/bet'
import Decimal from 'decimal.js-light'
import { FullBetType } from './useBettingBoard'
import useVisibility from './useVisibility'

export interface DragSessionType {
  /**
   * The bet type that was selected when the pointer is released.
   * This is used to determine if the bet is valid,
   * specifically for the case where the pointer is released outside the betting board,
   * or areas that are not accepting release events.
   */
  currBetType: FullBetType | null

  /**
   * The bet type that was selected when the press began.
   */
  preBetType: FullBetType | null

  /**
   * The screen coordinates `{ x, y }` of the pointer at the instant
   * the press event occurred.
   */
  prePosit: { x: number; y: number }

  /**
   * `true` once the pointer movement has passed the drag threshold.
   * Indicates an active drag operation.
   */
  isDrag: boolean

  /**
   * The distance from the edges of the chip
   * This is used to snap the chip to the exact position when user presses down the mouse
   */
  offset: { x: number; y: number }
}

const DEFAULT_SESSION = {
  preBetType: null,
  currBetType: null,
  isDrag: false,
  prePosit: { x: 0, y: 0 },
  offset: { x: 0, y: 0 },
}

export interface ChipActionPayload {
  betType: FullBetType
  newBets: { [x in FullBetType]: number }
  isNormalPlaceBet: boolean
  position: { x: number; y: number } // center position of div
  latestPlaceBet: { [x in string]: number }
}

/**
 * @hook useChipActions
 * @description Hook that manages chip interactions: click and drag.
 * It handles drag and drop actions, calculates bet values, and manages the drag session state.
 * * Hook should only be responsible for managing chip actions and NOT any betting logic.
 *
 * @returns
 * @payload - The current chip action payload, which includes the bet type, new bets, position, and other related data.
 * @dragSession - The current drag session state, which includes information about the bet type, position, and whether the chip is being dragged.
 * @draggedChipRef - A reference to the currently dragged chip element.
 * @setDragSession - A function to update the drag session state.
 * @resetDragSession - A function to reset the drag session state.
 * @onPress - A function to handle the press event on a chip, which initiates the drag session.
 * @onRelease - A function to handle the release event on a chip, which finalizes the bet placement.
 */
const useChipAction = () => {
  const [payload, setPayload] = useState<ChipActionPayload | null>(null)

  const [dragSession, setDragSession] = useState<DragSessionType>(DEFAULT_SESSION)
  const dragSessionRef = useRef(dragSession)

  const isBet = useBettingStore((state) => state.isBet)

  const draggedChipRef = useRef<HTMLElement | null>(null)
  const isVisible = useVisibility()
  const boardHistory = useCartStore((s) => s.boardHistory)
  const isSlope = useBettingStore((s) => !s.isBet)
  const selectedChipVal = useCartStore((s) => s.selectedChipVal)
  const getTotalBet = useCartStore((s) => s.getTotalBet)
  const walletBalance = useCartStore((s) => s.walletBalance)

  // Note: Fixing the isDrag not updated by TrackerChip.onMove issue
  const setDragSessionWithRef = useCallback((newSession: DragSessionType) => {
    dragSessionRef.current = newSession
    setDragSession(newSession)
  }, [])

  const onPress = useCallback(
    (e: React.MouseEvent, key: FullBetType, draggedChip: HTMLElement | undefined) => {
      if (!key) return

      if (isSlope) return tooltipEventBus.emit('close', { position: { x: e.pageX, y: e.pageY }, mute: true })

      const edgeDist = getDraggedChipEdgeDist(e.pageX, e.pageY, draggedChip)

      if (!edgeDist || !draggedChip) {
        setDragSessionWithRef({ ...dragSessionRef.current, prePosit: { x: 0, y: 0 }, preBetType: key })
        return
      }

      draggedChipRef.current = draggedChip
      setDragSessionWithRef({
        preBetType: key,
        prePosit: { x: e.pageX, y: e.pageY },
        currBetType: null,
        isDrag: false,
        offset: { x: edgeDist.offsetX, y: edgeDist.offsetY },
      })
    },
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [isSlope, setDragSessionWithRef, draggedChipRef, boardHistory]
  )

  const onRelease = useCallback(
    (e: React.MouseEvent, betType: FullBetType, extraBetsGroup: string[]) => {
      const { preBetType, prePosit, isDrag } = dragSessionRef.current
      const draggedChip = draggedChipRef?.current
      const draggedChipVal = Number(draggedChip?.dataset.value) || 0

      if (!betType || !preBetType) return resetDragSession()

      const lastBet = boardHistory.at(-1) ?? {}
      const isSameSpot = preBetType === betType
      const isExtraBetsGroup = extraBetsGroup.includes(betType)
      setDragSessionWithRef({ ...dragSessionRef.current, currBetType: betType })

      // Prevent placing a bet when nothing is dragged
      if (!isDrag && !isSameSpot) return resetDragSession()
      // Prevent placing a bet when dropping in the same spot
      if (isDrag && isSameSpot) return resetDragSession()
      if (isDrag && isExtraBetsGroup) return resetDragSession()

      // ARO-255: if is not dragging over threshold, treat as normal place bet
      const isNormalPlaceBet = !prePosit.x || !prePosit.y || !isDrag

      const lastBetVal = new Decimal(lastBet[betType] || 0)
      const extra = new Decimal(isDrag ? draggedChipVal : selectedChipVal)
      const newBetVal = lastBetVal.plus(extra).toNumber()

      const newBets = { [betType]: extra.toNumber() } as { [x in FullBetType]: number }

      // Remove the previous bet after dragging
      if (!isSameSpot && preBetType) delete newBets[preBetType]

      const { x, y } = getCenterPosition(e)

      setPayload({
        betType,
        newBets,
        latestPlaceBet: { [betType]: newBetVal },
        isNormalPlaceBet,
        position: { x, y },
      })
    },
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [setDragSessionWithRef, boardHistory, getTotalBet, selectedChipVal, walletBalance]
  )

  const resetDragSession = useCallback(() => {
    const preChip = draggedChipRef.current
    setTimeout(() => preChip?.style.setProperty('opacity', '1'), 10)
    draggedChipRef.current = null
    setDragSessionWithRef(DEFAULT_SESSION)
  }, [setDragSessionWithRef])

  useEffect(() => {
    dragSessionRef.current = dragSession
  }, [dragSession])

  useEffect(() => {
    // Leaving tab whiling drag session is present.
    if (!isVisible) resetDragSession()
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isVisible])

  useEffect(() => {
    if (!isBet) resetDragSession()
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isBet])

  return { payload, dragSession, draggedChipRef, setDragSession: setDragSessionWithRef, resetDragSession, onPress, onRelease }
}

export default useChipAction
