import { memo } from 'react'
import { useTranslation } from 'react-i18next'

import TabSwitch, { TabListType } from '@repo/ui/components/common/tabSwitch/TabSwitch'
import { useSettingStore, VideoQuality } from '@repo/ui/store/useSettingStore'

function VideoTabs() {
  const activeTab = useSettingStore((state) => state.videoQuality)
  const onTabClick = (tab: VideoQuality) => useSettingStore.setState({ videoQuality: tab })
  const { t } = useTranslation()

  const tabList: TabListType<VideoQuality> = [
    { id: 'AUTO', label: t('COMMON.VIDEO_AUTO') },
    { id: 0, label: t('COMMON.VIDEO_LOW') },
    { id: 1, label: t('COMMON.VIDEO_MEDIUM') },
    { id: 2, label: t('COMMON.VIDEO_HIGH') },
    { id: 3, label: t('COMMON.VIDEO_HD') },
  ]

  return (
    <TabSwitch
      list={tabList}
      activeTab={activeTab}
      onClick={onTabClick}
      wrapperClass="pb-4"
      tabContainerClass="h-[2.25rem]"
    />
  )
}

export default memo(VideoTabs)
