import usePreventPullToRefresh from '@repo/utils/hook/usePreventPullToRefresh'
import { useStatisticStore } from '@roulette/stores/useStatisticStore'
import { useRef } from 'react'
import StatisticAdvanced from './Advanced/StatisticAdvanced'
import StatisticHotCold from './StatisticHotCold'
import StatisticLast500 from './StatisticLast500'
import './Statistics.scss'

const Statistic = () => {
  const activeTab = useStatisticStore((state) => state.activeTab)

  const scrollWrapRef = useRef<HTMLDivElement>(null)
  usePreventPullToRefresh(scrollWrapRef)

  return (
    <div
      ref={scrollWrapRef}
      id="Statistic"
      className="scrollWrap mx-auto h-full w-full px-[0.875rem]"
    >
      {activeTab === 'hotCold' && <StatisticHotCold />}
      {activeTab === 'last500' && <StatisticLast500 />}
      {activeTab === 'advanced' && <StatisticAdvanced />}
    </div>
  )
}

export default Statistic
